<template>
  <div class="publicHeader">
    <div class="header_first">
      <div class="header_first_box">
        <ul class="header_first_list">
          <router-link to="/index"><li class="list_left">首页</li></router-link>
          <li class="list_left">加盟合作</li>
          <li class="list_left">媒体采访</li>
          <img style="float: left; margin-top: 8px; margin-right: 2px;" src="../../images/mobilephone.png" alt="">
          <li class="list_left">玩转手机版</li>
          <router-link to="/checkShop"><li class="list_right">购物车{{num}}件</li></router-link>
          <img style="float: right; margin-top: 8px; margin-right: 2px;" src="../../images/shopcar.png" alt="">
          <li class="list_right" @click="logIn()">登录</li>
          <router-link to="/register"><li class="list_right">注册</li></router-link>
          <li class="list_right" @click="mineForm()">个人中心</li>
        </ul>
      </div>
    </div>
    <div class="clearfix"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'publicHeader',
    data () {
      return {
        add: this.GLOBAL.address,
        num: 0,
        token: '',
      }
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      that.token = token;
      axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        var number = res.data.data;
        console.log(number.length);
        that.num = number.length;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      mineForm: function() {
        var that = this;
        let token = window.localStorage.getItem('token');
        console.log(token);
        if(token == '') {
          that.$router.push({ path: '/logIn' });
        }else {
          that.$router.push({ path: '/mineForm' });
        }
      },
      logIn: function() {
        var that = this;
        let token = window.localStorage.getItem('token');
        console.log(token);
        if(token == undefined) {
          that.$router.push({ path: '/logIn' });
        }else {
          that.$message('你已登录！');
          return false
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul {
    list-style-type: none;
  }

  a {
    text-decoration: none;
    color: #83868E;
  }

  .publicHeader {
    width: 100%;
    /*    margin-bottom: 2px;*/
  }
  /*-------header_first_box------*/
  .publicHeader .header_first {
    width: 100%;
    height: 30px;
    background-color: #F8F8F8;
    text-align: center;
  }

  .publicHeader .header_first .header_first_box {
    margin: 0 auto;
    width: 1024px;
    height: 30px;
    background-color: #F8F8F8;
  }

  .header_first_box .header_first_list {
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #83868E;
    font-size: 12px;
  }

  .header_first_box .header_first_list .list_left {
    margin-right: 20px;
    float: left;
  }

  .header_first_box .header_first_list .list_right {
    margin-right: 20px;
    float: right;
  }

  .header_first_box .header_first_list .list_right:hover {
    background-color: #FFA000;
    color: #fff;
    cursor: pointer;
    transition: all 0.5s ease;
  }

  .header_first_box .header_first_list .list_left:hover {
    background-color: #FFA000;
    color: #fff;
    cursor: pointer;
    transition: all 0.5s ease;
  }
  /*-------header_second_box------*/
</style>



