<template>
  <div class="wzCarousel">
    <div class="block">
      <el-carousel height="400px">
        <el-carousel-item v-for="item in items" :key=item>
          <img :src=item.picture alt="img">
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="slogan_box">
      <div class="slogan">
        <img class="slogan_img" src="../../images/realname.png" alt="Polygon">
        <p class="slogan_header">资质认证</p>
        <p class="slogan_des">服务商100%实名审核认证</p>
      </div>
      <div class="slogan">
        <img class="slogan_img" src="../../images/security.png" alt="Polygon">
        <p class="slogan_header">安全支付</p>
        <p class="slogan_des">明码标价支付及信息安全</p>
      </div>
      <div class="slogan">
        <img class="slogan_img" src="../../images/insurance.png" alt="Polygon">
        <p class="slogan_header">保险赔付</p>
        <p class="slogan_des">太平洋保险提供担保赔付</p>
      </div>
      <div class="slogan">
        <img class="slogan_img" src="../../images/aftersale.png" alt="Polygon">
        <p class="slogan_header">售后无忧</p>
        <p class="slogan_des">服务问题售后经理全程跟进</p>
      </div>
    </div>
    <div class="clearfix"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'wzCarousel',
    data () {
      return {
        add: this.GLOBAL.address,
        items: [],
      }
    },
    mounted: function() {
      var that = this;
      axios.get(that.add + '/api/v1.0/home/carousel',{
      })
      .then(function (res) {
          console.log(res.data.data);
          that.items = res.data.data;
          }).catch(function (error) {
            console.log(error);
          });
        }
      }
    </script>

    <!-- Add "scoped" attribute to limit CSS to this component only -->
    <style scoped>
      .block {
        width: 1024px;
        height: 400px;
        margin: 0 auto;
      }
      .el-carousel__item h3 {
        text-align: center;
        color: #475669;
        font-size: 15px;
        opacity: 0.75;
        line-height: 400px;
        margin: 0;
      }

      .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
      }

      .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
      }

      .slogan_box {
        margin: 0 auto;
        width: 1024px;
        height: 218px;
      }

      .slogan {
        float: left;
        width: 256px;
        height: 218px;
        text-align: center;
      }

      .slogan .slogan_img {
        margin-top: 36.58px;
        margin-bottom: 17.58px;
      } 

      .slogan .slogan_header {
        font-size: 18px;
        color: #555964;
        margin-bottom: 7px;
      }

      .slogan .slogan_des {
        font-size: 14px;
        color: #83868E;
      }
    </style>


