import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/index'
import logIn from '@/components/logIn/logIn'
import Register from '@/components/logIn/register'
import ForgetPw from '@/components/logIn/forgetPw'
import checkShopping from '@/components/shoppingCart/checkShopping'
import confirmOrder from '@/components/shoppingCart/confirmOrder'
import orderSuccess from '@/components/shoppingCart/orderSuccess'
import paySuccess from '@/components/shoppingCart/paySuccess'
import payFail from '@/components/shoppingCart/payFail'
import mineForm from '@/components/mine/mineForm'
import layer from '@/components/layer/layerService'
import business from '@/components/business/businessService'
import tax from '@/components/tax/taxService'
import detail from '@/components/detail/detail'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    { path: '/', redirect: { name: 'index' }},
    {
      path: '/index',
      name: 'index',
      component: Index,
    },
    {
      path: '/login',
      name: 'logIn',
      component: logIn
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/forgetPw',
      name: 'forgetPw',
      component: ForgetPw
    },
    {
      path: '/checkShop',
      name: 'checkShopping',
      component: checkShopping
    },
    {
      path: '/confirmOrder',
      name: 'confirmOrder',
      component: confirmOrder
    },
    {
      path: '/orderSuccess',
      name: 'orderSuccess',
      component: orderSuccess
    },
    {
      path: '/success',
      name: 'paySuccess',
      component: paySuccess
    },
    {
      path: '/fail',
      name: 'payFail',
      component: payFail
    },
    {
      path: '/mineForm',
      name: 'mineForm',
      component: mineForm
    },
    {
      path: '/layer',
      name: 'layer',
      component: layer
    },
    {
      path: '/business',
      name: 'business',
      component: business
    },
    {
      path: '/tax',
      name: 'tax',
      component: tax
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: detail
    }
  ]
})



