<template>
  <div class="detail">
    <HeaderTop v-bind:num="num"></HeaderTop>
    <div class="meal_box">
      <img class="meal_img" :src=goods.pictureOne alt="img">
      <div class="right_box">
        <p class="meal_title">{{goods.caption}}</p>
        <p class="meal_des">{{goods.description}}</p>
        <div class="meal_price_box">
          <p class="market_price_word">市场价：<span class="market_price">¥{{goods.price}}</span></p>
          <p class="enjoy_price_word">体验价：<span class="enjoy_price">¥{{goods.discount}}</span><span class="special">元/年</span></p>
        </div>
        <div class="phoneTalk" @click="tel()">电话咨询</div>
        <div class="addIn" @click="addGoods()">
          <img class="addIn_img" src="../../images/shopcarWhite.png" alt="img"><span class="addIn_word">加入购物车</span>
        </div>
      </div>
    </div>
    <div class="detail_box">
      <div class="left_side">
        <div class="left_side_title">单项服务</div>
        <div class="left_side_item" v-for="item in items" :key="item" @click="detail(item)">
          <img :src=item.pictureOne alt="img">
          <div class="left_side_item_detail">
            <span class="item_title">{{item.caption}}</span>
            <span class="item_price">¥{{item.price}}<span style="font-size:14px;">起</span></span>
          </div>
        </div>
      </div>
      <div class="right_side">
        <div class="right_side_title">
          <div class="right_side_item" :class="{ active: isActive1 }" @click="changeActive1(tab01Text);">服务介绍</div>
          <div class="right_side_item" :class="{ active: isActive2 }" @click="changeActive2(tab02Text);">用户评价 <b style="color: #FFA000;">12</b></div>
          <div class="right_side_item" style="border-right: 1px #EBEBEB solid;" :class="{ active: isActive3 }" @click="changeActive3(tab03Text);">服务保障</div>
        </div> 
        <div class="clearfix"></div>
        <!-- 点击导航后要切换的内容 -->
        <transition name="component-fade" mode="out-in">
          <!-- 如果把切换出去的组件保留在内存中，可以保留它的状态或避免重新渲染。为此可以添加一个 keep-alive 指令参数 -->
          <component :is="currentView" keep-alive></component>
        </transition> 
      </div>
    </div>
    <div class="clearfix" style="margin-bottom: 100px;"></div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import HeaderTop from '../index/header'
  import footerBottom from '../index/footer'
  import introduction from './introduction'
  import comment from './comment' 
  import guarantee from './guarantee'
//扩展组件tab02
// var tab02 = Vue.extend({
//   template: '<div>this is 待评价2</div>',
// });
//   Vue.component('HeaderTop', {
//   // camelCase in JavaScript
//   props: ['goodsNum'],
// })
export default {
  name: 'detail',
  data () {
    return {
      add: this.GLOBAL.address,
      objectId: '',
      goods: [],
      items: [],
      shops: [],
      num: 0,
      tab01Text: "tab01",
      tab02Text: "tab02",
      tab03Text: "tab03", 
      currentView: 'tab01', //默认选中的导航栏
      isActive1: true,
      isActive2: false,
      isActive3: false,
    }
  },
  components: {
    HeaderTop,
    footerBottom,
    tab01: introduction,
    tab02: comment,
    tab03: guarantee,
  },
  watch: {
    shops: function() {
      var that = this;
      var shops = that.shops;
      that.num = shops.length
      //console.log(that.num,'hah');
    }
  },
  //*******导航钩子*********//
  beforeRouteEnter (to, from, next) {
    // 通过 `vm` 访问组件实例
    next(vm => {
      var that = vm;
      var objectId = that.$route.params.id;
      that.objectId = objectId;
      var token = window.sessionStorage.getItem('token');
      axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
        params: {
          token: token
        }
      }).then(function (res) {
        var shops = res.data.data;
        that.num = shops.length;
      })
      .catch(function (error) {
        console.log(error);
      });
      //console.log(that.objectId);
      axios.get( that.add + '/api/v1.0/goods/details',{
        params: {
          objectId: that.objectId
        }
      })
      .then(function (res) {
        console.log(res.data.data);
        that.goods = res.data.data;
      })
      .catch(function (error) {
        console.log(error);
      });
    })
  },
  mounted: function() {
    var that = this;
    axios.get( that.add + '/api/v1.0/goods/list',{
    })
    .then(function (res) {
      var a = res.data.data;
      //console.log(a);
      var b = a.filter(function(ele,index,arr) {
          // console.log(ele);
          return ele.type == '法律维权-普通';   
      });
      var c = a.filter(function(ele,index,arr) {
          // console.log(ele);
          return ele.type == '工商服务-普通';   
      });
      var d = a.filter(function(ele,index,arr){
          // console.log(ele);
          return ele.type == '财税服务-普通';   
      });
      var arr = b.concat(c,d);
      that.items = arr.sort(function(){ return 0.5 - Math.random() })
      //console.log(str);
      //that.items = b.concat(c, d);
      //console.log(that.items, 'frisco');
      window.scroll(0,0);
      // console.log(b);
    })
    .catch(function (error) {
      console.log(error);
    });
  },
  methods: {
    //绑定tab的切换事件
    changeActive1: function(tabText) {
      this.currentView = tabText;
      this.isActive1 = true;
      this.isActive2 = false;
      this.isActive3 = false;
    },
    changeActive2: function(tabText) {
      this.currentView = tabText;
      this.isActive1 = false;
      this.isActive2 = true;
      this.isActive3 = false;
    },
    changeActive3: function(tabText) {
      this.currentView = tabText;
      this.isActive1 = false;
      this.isActive2 = false;
      this.isActive3 = true;
    },
    detail: function(item) {
      var that = this;
      // console.log(item._id);
      that.$router.push({ name: 'detail', params: { id: item._id}});
      that.$router.go(0);
      window.scroll(0,0);
    },
    //加入购物车
    addGoods: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      var item = that.goods;
      //console.log(item);
      axios({
        method: 'post',
        url: that.add + '/api/v1.0/order/shoppingcart_add',
        data: `objectId=${item._id}&token=${token}`
      })
      .then(function (res) {
        if (res.data.errcode == 404) {
          that.$message.error('商品添加失败，请先登录！');
        } else {
          axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
            params: {
              token: token
            }
          })
          .then(function (res) {
            if (res.data.errcode == 404) {
              that.$message.error('商品添加失败!');
            } else {
              //console.log(res);
              that.shops = res.data.data;
              that.$message({
                message: '商品添加成功！',
                type: 'success'
              });
              that.$router.push({path: '/checkShop'});
            }
            // that.$router.go(0);
          })
          .catch(function (error) {
            console.log(error);
          });
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*-------animation start------*/
  .component-fade-enter-active, .component-fade-leave-active {
    transition: opacity .3s ease-out;
  }
  .component-fade-enter, .component-fade-leave-active {
    opacity: 0;
  }
  /*-------animation end-------*/ 
  .meal_box {
    width: 1024px;
    height: 330px;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
    border: 1px solid #EBEBEB;
  }
  .meal_img {
    width: 290px;
    height: 290px;
    float: left;
    margin-left: 20px;
    margin-top: 20px;
  }
  .right_box {
    margin-left: 20px;
    margin-top: 20px;
    width: 692px;
    height: 290px;
    float: left;
  }
  .meal_title {
    margin-top: 10px;
    font-size: 24px;
    color: #555964;
    margin-bottom: 10px;
  }
  .meal_des {
    width: 660px;
    font-size: 14px;
    color: #83868E;
    margin-bottom: 12px;
  }
  .meal_price_box {
    width: 692px;
    height: 78px;
    background-color: #FBFBFB;
    color: #83868E;
    font-size: 12px;
  }
  .market_price_word {
    padding-top: 17px;
    margin-left: 10px;
  }
  .market_price {
    margin-left: 10px;
    text-decoration: line-through;
  }
  .enjoy_price_word {
    margin-left: 10px;
  }
  .enjoy_price {
    color: #E4393C;
    font-size: 28px;
    font-weight: bold;
  }
  .special {
    color: #555964;
    font-size: 14px;
  }
  .phoneTalk {
    float: left;
    width: 130px;
    height: 40px;
    line-height: 41px;
    text-align: center;
    background-color: rgba(255,160,0,0.2);
    color: #F7941E;
    font-size: 14px;
    margin-top: 20px;
    margin-left: 5px;
  }
  .addIn {
    float: left;
    width: 170px;
    height: 40px;
    line-height: 41px;
    text-align: center;
    background-color: #FFA000;
    color: #fff;
    font-size: 14px;
    margin-top: 20px;
    margin-left: 20px;
    position: relative;
  }
  .addIn:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  .addIn_img {
    position: absolute;
    top: 12px;
    left: 39px;
  }
  .addIn_word {
    position: absolute;
    top: 0px;
    left: 60px;
  }
  /*------detail_box start------*/
  .detail_box {
    width: 1024px;
    height: auto;
    margin: 0 auto;
  }
  .left_side {
    width: 220px;
    /*height: 2202px;*/
    border: 1px solid #EBEBEB;
    float: left;
  }
  .right_side {
    width: 780px;
    /* height: 2202px;*/
    /*background-color: #FFA300;*/
    float: right;
    border: 1px solid #EBEBEB;
  }
  .left_side_title {
    width: 220px;
    height: 46px;
    line-height: 47px;
    background-color: #FAFAFA;
    color: #83868E;
    font-size: 14px;
    text-align: center;
  }
  .left_side_item {
    width: 160px;
    height: 188px;
    margin-top: 30px;
    margin-left: 30px;
    position: relative;
  }
  .left_side_item:hover {
    opacity: 0.8;
    cursor: pointer;
  }
  .left_side_item_detail {
    width: 160px;
    height: 28px;
    position: absolute;
    bottom: 0;
  }
  .item_title {
    color: #83868E;
    font-size: 14px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .item_price {
    color: #FF7A00;
    font-size: 18px;
    position: absolute;
    bottom: -2px;
    right: 0;
  }

  .right_side_title {
    width: 780px;
    height: 50px;
    background-color: #FAFAFA;
    color: #83868E;
    font-size: 14px;
  }

  .right_side_item {
    width: 140px;
    height: 46px;
    line-height: 47px;
    text-align: center;
    float: left;
    border-top: 1px #EBEBEB solid;
    border-bottom: 1px #EBEBEB solid;
    border-left: 1px #EBEBEB solid;
  }
  .right_side_item:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  .active {
    border-top: 1px solid #FFA000;
    color: #FFA000;
    background-color: #fff;
  }
  /*------detail_box end------*/

</style>



