<template>
  <div class="changePass">
    <div class="receive_box">
      <span class="recevie_people">旧密码：</span>
      <input type="password" class="receiveName" id="receiveName" placeholder="" v-model="oldPwd">
      <transition name="shake">
        <span v-show="oldAlert" class="remind">请输入有效的旧密码！</span>
      </transition>
    </div>
    <div class="detailAdd_box">
      <span class="detailAdd_word">新密码：</span>
      <input type="password" class="detailAdd" id="detailAdd" placeholder="" v-model="newPwd">
      <transition name="shake">
        <span v-show="newAlert" class="remind">请输入有效的新密码！密码长度需在8到16位之间</span>
      </transition>
    </div>
    <div class="confirm_box">
      <span class="confirm_word">确认密码：</span>
      <input type="password" class="confirm" id="confirm" placeholder="" v-model="conPwd">
      <transition name="shake">
        <span v-show="confirmAlert" class="remind">两次输入密码不一致！</span>
      </transition>
    </div>
    <el-button class="confirm_btn" @click="confirm()">确定</el-button>
  </div>
</template>

<script>
  import axios from 'axios'
  var crypto = require('crypto');
  export default {
    name: 'changePass',
    data () {
      return {
        add: this.GLOBAL.address,
        oldPwd: '',
        newPwd: '',
        conPwd: '',
        oldAlert: false,
        newAlert: false,
        confirmAlert: false,
        username: '',
      }
    },
    components: {
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      that.token = token;
      axios.get(that.add + '/api/v1.0/user/userinfo',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        console.log(res.data.data);
        that.username = res.data.data.username;
      // console.log(that.token);
      // that.src = res.data.data.head;
    })
      .catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      confirm: function() {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        console.log(token,that.oldPwd,that.newPwd,that.conPwd);
        if(that.oldPwd.length<8 || that.oldPwd.length>16 || that.oldPwd == '') {
          that.oldAlert = true;
          return false
        }
        if(that.newPwd.length<8 || that.newPwd.length>16 || that.newPwd == '') {
          that.newAlert = true;
          return false
        }
        if(that.conPwd !== that.newPwd || that.conPwd == '') {
          that.confirmAlert = true;
          return false
        }
        axios.get( that.add + '/api/v1.0/auth/salt',{
          params: {
            mobilephone: that.username
          }
        }).then(function (res) {
          //console.log(res);
          // console.log(pwd);
          //获取hash值
          crypto.pbkdf2(that.oldPwd, res.data.data, 4096, 256, function (err, hash) {
            // console.log(hash.toString('hex'));
            var Hash = hash.toString('hex')
            axios({
              method: 'post',
              url: that.add +'/api/v1.0/user/changepassword',
              data: `token=${token}&pwd=${Hash}&newpwd=${that.newPwd}`
            }).then(function (res) {
              if(res.data.errcode == 404) {
                that.$message.error('修改密码失败，输入信息有误！');
                return false
              }else {
                that.oldPwd = '',
                that.newPwd = '',
                that.conPwd = '',
                that.$message({
                  type: 'success',
                  message: '修改密码成功!'
                });
              }
            }).catch(function (error) {
              console.log(error);
            });  
          })
        }).catch(function (error) {
          console.log(error);
        });  
        // axios({
        //   method: 'post',
        //   url: that.add +'/api/v1.0/user/changepassword',
        //   data: `token=${token}&pwd=${that.oldPwd}&newpwd=${that.newPwd}`
        // }).then(function (res) {
        //   console.log(res.data);
        //   if(res.data.errcode == 404) {
        //     that.$message.error('修改密码失败，输入信息有误！');
        //     return
        //   }else {
        //     that.oldPwd = '',
        //     that.newpwd = '',
        //     that.conPwd = '',
        //     that.$message({
        //       type: 'success',
        //       message: '修改密码成功!'
        //     });
        //   }
        // }).catch(function (error) {
        //   console.log(error);
        // });  
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*过渡动画开始*/
  .shake-enter-active {
    animation: shake .5s;
  }
  .shake-leave-active {
    transition: all 0.3s ease;
    opacity: 0;
  }
  @-webkit-keyframes shake {
    from, to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    10%, 30%, 50%, 70%, 90% {
      -webkit-transform: translate3d(-7px, 0, 0);
      transform: translate3d(-7px, 0, 0);
    }

    20%, 40%, 60%, 80% {
      -webkit-transform: translate3d(7px, 0, 0);
      transform: translate3d(7px, 0, 0);
    }
  }

  @keyframes shake {
    from, to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    10%, 30%, 50%, 70%, 90% {
      -webkit-transform: translate3d(-7px, 0, 0);
      transform: translate3d(-7px, 0, 0);
    }

    20%, 40%, 60%, 80% {
      -webkit-transform: translate3d(7px, 0, 0);
      transform: translate3d(7px, 0, 0);
    }
  }

  .shake {
    -webkit-animation-name: shake;
    animation-name: shake;
  }
  /*过渡动画结束*/
  input {
    border: none;
    outline:medium;
  }
  .el-button {
    border: none;
  }

  .el-button:hover {
    color: #fff;
    border: none;
  }
  .receive_box {
    margin-left: 30px;
    margin-top: 39px;
    position: relative;
    color: #555964;
    font-size: 14px;
  }

  .receive_box .recevie_people {
    margin-right: 11px;
  }

  .receive_box #receiveName {
    width: 160px;
    height: 24px;
    border: 1px solid #E0E0E0;
  }

  .detailAdd_box {
    margin-left: 31px;
    margin-top: 10px;
    position: relative;
    color: #555964;
    font-size: 14px;
  }

  .detailAdd_box .detailAdd_word {
    margin-right: 10px;
  }

  .detailAdd_box #detailAdd {
    width: 160px;
    height: 24px;
    border: 1px solid #E0E0E0;
  }

  .confirm_box {
    margin-left: 17px;
    margin-top: 10px;
    position: relative;
    color: #555964;
    font-size: 14px;
  }

  .confirm_box .confirm_word {
    margin-right: 10px;
  }

  .confirm_box #confirm {
    width: 160px;
    height: 24px;
    border: 1px solid #E0E0E0;
  }

  .confirm_btn {
    margin-left: 102px;
    margin-top: 30px;
    width: 80px;
    height: 30px;
    line-height: 10px;
    text-align: center;
    border-radius: 3px;
    background-color: #FFA000;
    color: #fff;
    font-size: 14px;
  }

  .confirm_btn:hover {
    cursor: pointer;
    background-color: #FF7A00;
    transition: all 0.5s ease;
  }

  .remind {
    color: #D0011B;
    font-size: 12px;
    position: absolute;
    top: 2px;
  }
</style>



