    //*******导航钩子*********//
    beforeRouteEnter (to, from, next) {
      // 通过 `vm` 访问组件实例
      next(vm => {
        var that = vm;
        var token = window.sessionStorage.getItem('token');
        that.allCount = that.$route.params.allCount;
        that.allMoney = that.$route.params.allMoney;
        that.checked = that.$route.params.checked;
        axios.get(that.add + '/api/v1.0/user/addresses_list',{
          params: {
            token: token
          }
        }).then(function (res) {
          var a = res.data.data;
          var b = a.filter(function(ele,index,arr){
            return ele.active == true;   
          });
          that.addId = b[0]._id;
          that.shop = res.data.data;
          that.numValue = that.shop.length;   
          console.log(that.shop);  
          if (that.shop.length == 0) {
            that.dialogFormVisible = true;
          }
        }).catch(function (error) {
          console.log(error);
        });
      })
    },
    watch: {
      del: function() {
        var that = this;
        if (that.shop.length == 0) {
          that.dialogFormVisible = true;
        } else {
          that.dialogFormVisible = false;
        }
      }
