    },
    methods: {
      //选择地址
      chooseAd: function(add) {
        var that = this;
        that.array_b = [];
        add.active= !add.active;
        if (add.active == true) {
          var a = that.shop;
          var b = a.some(function(ele,index,arr){
            that.array_b.push(ele);
            return ele._id == add._id;
          });
          var k = that.array_b.length - 1;
          for (var i = 0; i < that.shop.length; i++) {
            that.shop[i].active = false;
            that.shop[k].active = true;
            that.addId = add._id;
          }
          console.log(that.addId);
          //console.log(that.array_b.length,22)
          //console.log(k,22);
          //console.log(b, 'ff');
