        } else {
          console.log('frisco');
        }
      },
      //保存地址
      save: function(add) {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        if(that.form.name == '') { 
          that.realAlert1 = true;
          that.$message.error('修改地址失败！');
          return false
        }
        if(that.form.region == '') {
          that.placeAlert1 = true;
          that.$message.error('修改地址失败！');
          return false
        }
        if(that.form.address == '') {
          that.addAlert1 = true;
          that.$message.error('修改地址失败！');
          return false
        }
        if(that.form.mobile == '') {
          that.phoneAlert1 = true;
          that.$message.error('修改地址失败！');
          return false
        }
        // console.log(item._id);
        axios({
          method: 'post',
          url: that.add + '/api/v1.0/user/addresses_add',
          data: `realname=${that.form.name}&district=${that.form.region}&street=${that.form.address}&mobilephone=${that.form.mobile}&token=${token}&active=${that.setDefault}`,
        }).then(function(res){
          if (res.data.errcode == 200) {
            that.form.name = '',
            that.form.region = '',
            that.form.address = '',
            that.form.mobile = '',
            that.realAlert1 = false,
            that.addAlert1 = false,
            that.placeAlert1 = false,
            that.phoneAlert1 = false,
            axios.get(that.add + '/api/v1.0/user/addresses_list',{
              params: {
                token: token
              }
            }).then(function (res) {
              that.shop = res.data.data;
              that.$message({
                type: 'success',
                message: '新增地址成功!'
              });
              that.dialogFormVisible = false;
              // that.add_num = that.address.length;
            }).catch(function (error) {
              console.log(error);
            });
          } else {
            console.log('新增地址失败');
          }
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
        });  
      },
      //删除地址
      del: function(add) {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        that.$confirm('此操作将永久删除该地址, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios({
            method: 'post',
            url: that.add + '/api/v1.0/user/addresses_del',
            data: `addrId=${add._id}&token=${token}`
          }).then(function(res){
            if(res.data.errcode == 200) {
              axios.get( that.add + '/api/v1.0/user/addresses_list',{
                params: {
                  token: token
                }
              }).then(function (res) {
                if(res.data.errcode == 200) {
                  that.shop = res.data.data;
                  that.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                } else {
                  console.log('删除失败');
                }
              })
              .catch(function (error) {
                console.log(error);
              });
            } else {
              console.log('删除失败');
            }
          })
        }).catch(() => {
          that.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //提交订单
      submit: function() {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        var kk = [];
        for (var i = 0; i < that.checked.length; i++) {
          kk.push(that.checked[i]._id);
          // console.log(kk);
        }
        // console.log(kk);
        //var haha = kk.join("-");
        var haha = kk.toString();
        console.log(haha);
        if(that.addId == '') { 
          that.$message.error('请先选择一个邮寄地址！');
          return false
        }
        axios({
          method: 'post',
          url: that.add + '/api/v1.0/order/add',
          data: `objectId=${haha}&token=${token}&addressId=${that.addId}`
        }).then(function(res){
          console.log(res);
          that.$router.push({ name: 'orderSuccess', params: { allCount: that.allCount, allMoney: that.allMoney, checked: that.checked, addressId: that.addId }});
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
        });  
