<template>
  <div class="orderSuccess">
    <publicHeader v-bind:num="numValue"></publicHeader>
    <div class="logIn_head_box">
      <div class="logIn_head">
        <router-link to="/"><img class="logo_img" src="../../images/wz_logo.png" alt="image"></router-link>
        <img class="shopfirst" src="../../images/shopSecond.png" alt="image">
      </div>
    </div>
    <div class="success_box">
      <div class="success_inside">
        <img class="left success" src="../../images/success.png" alt="images">
        <p class="first">订单提交成功！马上付款～</p>
        <p class="second">应付金额：¥{{allMoney}}</p>
        <p class="third">请在 <span style="color: #F6A623;">1分59分</span> 内完成支付，超时后将取消订单</p>
        <p class="fouth">发票邮寄地址：{{successAdd.district}}{{successAdd.street}} {{successAdd.realname}} {{successAdd.mobilephone}}</p>
<!--         <p class="fifth">商品名称：法律维权</p> -->
      </div>
    </div>
    <div class="pay_box">
      <div class="choosePay">请选择以下支付方式付款</div>
      <div class="payPlatform">支付平台</div>
      <ul class="platform_box">
        <li class="left platform"><img src="../../images/bank.png" alt="img"></li>
        <li class="left platform"><img src="../../images/bank.png" alt="img"></li>
        <li class="left platform"><img src="../../images/bank.png" alt="img"></li>
      </ul>
      <div class="clearfix"></div>
      <div class="payPlatform">银行借记卡和信用卡</div>
      <ul class="bank_box" v-for="item in platform" :key="item.id" @click="choosePay(item)">
        <li class="left platform" :style="{ border: item.border }"><img src="../../images/bank.png" alt="img"></li>
      </ul>
      <div class="clearfix"></div>
      <div class="confirmPay" @click="confirmPay">确认支付</div>
    </div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import axios from 'axios'
  import publicHeader from '../index/publicHeader'
  import footerBottom from '../index/footer'
  export default {
    name: 'orderSuccess',
    data () {
      return {
        add: this.GLOBAL.address,
        dialogTableVisible: false,
        dialogFormVisible: false,
        styleObject: {
          border: '1px solid #E0E0E0'
        },
        allCount: 0,
        allMoney: 0,
        checked: [],
        addressId: '',
        objectId: '',
        formLabelWidth: '120px',
        platform: [{
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          address: 'www.baidu.com',
          id: 0
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 1
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 2
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 3
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 4
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 5
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 6
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 7
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 8
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 9
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 10
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 11
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 12
        },
        {
          images: '../../images/bank.png',
          border: '1px solid #E0E0E0',
          id: 13
        }],
        successAdd: {},

      }
    },
    components: {
      publicHeader,
      footerBottom
    },
    //*******导航钩子*********//
    beforeRouteEnter (to, from, next) {
      // 通过 `vm` 访问组件实例
      next(vm => {
        window.scroll(0,0);
        var that = vm;
        that.allCount = that.$route.params.allCount;
        that.allMoney = that.$route.params.allMoney;
        that.checked = that.$route.params.checked;
        that.addressId = that.$route.params.addressId;
        that.objectId = that.$route.params.orderId;
        console.log(that.objectId, that.addressId, 22);
        var token = window.sessionStorage.getItem('token');
        axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
          params: {
            token: token
          }
        }).then(function (res) {
          if(res.data.errcode == 200) {
            var shops = res.data.data;
            that.numValue = shops.length;
          } else {
            return false
          }
        })
        .catch(function (error) {
          console.log(error);
        });
        axios.get(that.add + '/api/v1.0/user/addresses_select',{
          params: {
            addressId: that.addressId
          }
        }).then(function (res) {
          if (res.data.errcode == 200) {
            that.successAdd = res.data.data;
          } else {
            console.log('获取地址失败！')
          }
        }).catch(function (error) {
          console.log(error);
        });
      })
    },
    mounted: function() {

    },
    methods: {
      //选择支付方式
      choosePay: function(item) {
        var that = this;
        console.log(item);
         if(that.platform[item.id].border == '1px solid #E0E0E0') {
            for (var i = 0; i < that.platform.length; i++) {
              that.platform[i].border = '1px solid #E0E0E0';
            }
            that.platform[item.id].border = '1px solid #FFA000';
         } else {
            for (var i = 0; i < that.platform.length; i++) {
              that.platform[i].border = '1px solid #E0E0E0';
            }
         }
      },
      //确认支付
      confirmPay: function() {
        var that = this;
        axios.get(that.add + '/api/v1.0/order/pay',{
          params: {
            objectId: that.objectId
          }
        }).then(function (res) {
          if (res.data.errcode == 200) {
            console.log(res);
            that.$router.push({path: '/success'});
          } else {
            console.log('支付失败！')
          }
        }).catch(function (error) {
          console.log(error);
        });
        // var arr = [];
        // for (var i = 0; i < that.platform.length; i++) {
        //   if (that.platform[i].border == '1px solid #FFA000') {
        //     arr.push(that.platform[i]);
        //   }
        // }
        // console.log(arr);
        // window.location.href = this.platform[0].address; 
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .left {
    float: left;
  }

  .logIn_head_box {
    width: 100%;
    height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .logIn_head {
    margin: 0 auto;
    width: 1024px;
    height: 70px;
    line-height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .logIn_head .logo_img {
    width: 275px;
    height: 56px;
    margin-top: 6.5px;
  }

  .shopfirst {
    margin-left: 243px;
  }
/* sucess_box start */
  .success_box {
    width: 1024px;
    height: 239px;
    margin: auto;
    background-color: #F5F5F5;
    padding-top: 30px;
  }

  .success_inside {
    color: #555964;
    width: 1024px;
    height: 216px;
    background-color: #fff;
  }
  
  .success_inside .success {
    margin-top: 34px;
    margin-left: 30px;
    margin-right: 39px;
    margin-bottom: 103px;
  }

  .success_inside .first {
    font-size: 20px;
    font-weight: bold;
    padding-top: 30px;
    margin-bottom: 10px;
  }

  .success_inside .second {
    font-size: 14px;
    margin-bottom: 10px;
  }

  .success_inside .third {
    width: 893px;
    font-size: 14px;
    padding-bottom: 20px;
    margin-left: 131px;
    border-bottom: 1px solid #E0E0E0;
  }

  .success_inside .fouth {
    font-size: 12px;
    margin-top: 15px;
  }

  .success_inside .fifth {
    font-size: 12px;
    margin-top: 15px;
  }
/* sucess_box end */
/* pay_box start */
  .pay_box {
    width: 964px;
    height: 414px;
    margin: auto;
    color: #555964;
    padding: 20px 30px 20px 30px;
    border-bottom: 23px solid #F5F5F5;
  }

  .pay_box .choosePay {
    font-size: 16px;
    padding-bottom: 20px;
    border-bottom: 1px solid #E0E0E0;
  }

  .pay_box .payPlatform {
    font-size: 14px;
    margin-top: 24px;
    margin-bottom: 14px;
  }

  .platform {
    width: 125px;
    height: 50px;
    line-height: 60px;
    margin-right: 10px;
    margin-bottom: 10px;
    text-align: center;
    border: 1px solid #E0E0E0;
  }

  .platform:hover {
    cursor: pointer;
    opacity: 0.8;
/*    background: url(../../images/choose.png) no-repeat;*/
  }

  .confirmPay {
    width: 200px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 18px;
    color: #fff;
    background-color: #FFA000;
    border-radius: 5px;
    margin-top: 20px;
    cursor: pointer;
  }
</style>



