<script>
  import Vue from 'vue'
  import axios from 'axios'
  import allForm from './allForm'
  import waitPay from './waitPay'
  import setHead from './setHead'
  import manageAdd from './manageAdd'
  // import headPhoto from './headPhoto'
  import changePass from './changePass'
  //import HeaderTop from '../index/header'
  import footerBottom from '../index/footer'
//扩展组件tab03
var tab03 = Vue.extend({
  template: '<div>this is 待评价</div>',
});
export default {
  name: 'mineForm',
  data () {
    return {
      add: this.GLOBAL.address,
      isActive: true,
      isActive1: false,
      isActive2: false,
      isActive3: false,
      myform: true,
      myset: false,
      myformC: '#555964',
      mysetC: '#83868E',
      formL: 62,
      setL: 51,
      setB1: 'bold',
      setB2: 'normal',
      setB3: 'normal',
      setB4: 'normal',
      hover1: 1,
      hover2: 0,
      hover3: 0,
      hover4: 0,
      tab01Text: "tab01", //导航栏文本1
      tab02Text: "tab02", //导航栏文本2
      tab03Text: "tab03", //导航栏文本3
      tab04Text: "tab04", //导航栏文本4
      // tab05Text: "tab05", //导航栏文本5
      tab06Text: "tab06", //导航栏文本6
      tab07Text: "tab07", //导航栏文本6
      currentView: 'tab01', //默认选中的导航栏
      currentView1: 'tab04', //默认选中的导航栏
      num: 0,
      logIn: true,
      logOut: false,
      register: true,
      username: false,
      info: {},
      mineA: true,
      detailA: false,
    }
  },
  components: {
    //HeaderTop,
    footerBottom,
    tab01: allForm,
    tab02: waitPay,
    tab03: tab03,
    tab04: setHead,
    // tab05: headPhoto,
    tab06: manageAdd,
    tab07: changePass,
  },
  //*******导航钩子*********//
  beforeRouteEnter (to, from, next) {
    // 通过 `vm` 访问组件实例
    next(vm => {
      var that = vm;
      var token = window.sessionStorage.getItem('token');
      axios.get( that.add + '/api/v1.0/user/userinfo',{
        params: {
          token: token
        }
      }).then(function (res) {
        //console.log(res);
        if(res.data.errcode == 404) {
          console.log('token失效');
          // that.$router.push({ path: '/logIn' });
        } else {
          that.info = res.data.data;
          that.logIn = false;
          that.register = false;
          that.logOut = true;
          that.username = true;
        }
      }).catch(function (error) {
        console.log(error);
      });
      axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
        params: {
          token: token
        }
      }).then(function (res) {
        var shops = res.data.data;
        that.num = shops.length;
      })
      .catch(function (error) {
        console.log(error);
      });
    })
  },
  created: function() {
    var that = this;
    var token = window.sessionStorage.getItem('token');
      // that.allCount = that.$route.params.allCount;
      //console.log(token);
      axios.get( that.add + '/api/v1.0/user/userinfo',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        console.log(res);
        if(res.data.errcode == 404) {
          console.log('token失效');
          // that.$router.push({ path: '/logIn' });
        } else {
          that.info = res.data.data;
          that.logIn = false;
          that.register = false;
          that.logOut = true;
          that.username = true;
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        var number = res.data.data;
        //console.log(number.length);
        that.num = number.length;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    methods: {
    showMsgFromChild: function(data) {
      var that = this;
      that.userhead = data;
      //console.log(data, 'frisco');
    },
    //绑定tab的切换事件
    changeActive1: function(tabText) {
      this.currentView = tabText;
      this.setB1 = 'bold';
      this.setB2 = 'normal';
      this.setB3 = 'normal';
      this.hover1 = 1;
      this.hover2 = 0;
      this.hover3 = 0;
    },
    changeActive2: function(tabText) {
      this.currentView = tabText;
      this.setB1 = 'normal';
      this.setB2 = 'bold';
      this.setB3 = 'normal';
      this.hover1 = 0;
      this.hover2 = 1;
      this.hover3 = 0;
    },
    changeActive3: function(tabText) {
      this.currentView = tabText;
      this.setB1 = 'normal';
      this.setB2 = 'normal';
      this.setB3 = 'bold';
      this.hover1 = 0;
      this.hover2 = 0;
      this.hover3 = 1;
    },
    changeActive4: function(tabText) {
      this.currentView1 = tabText;
      this.setB1 = 'bold';
      this.setB2 = 'normal';
      this.setB3 = 'normal';
      this.setB4 = 'normal';
      this.hover1 = 1;
      this.hover2 = 0;
      this.hover3 = 0;
      this.hover4 = 0;
    },
    changeActive5: function(tabText) {
      this.currentView1 = tabText;
      this.setB1 = 'normal';
      this.setB2 = 'bold';
      this.setB3 = 'normal';
      this.setB4 = 'normal';
      this.hover1 = 0;
      this.hover2 = 1;
      this.hover3 = 0;
      this.hover4 = 0;
    },
    changeActive6: function(tabText) {
      this.currentView1 = tabText;
      this.setB1 = 'normal';
      this.setB2 = 'normal';
      this.setB3 = 'bold';
      this.setB4 = 'normal';
      this.hover1 = 0;
      this.hover2 = 0;
      this.hover3 = 1;
      this.hover4 = 0;
    },
    changeActive7: function(tabText) {
      this.currentView1 = tabText;
      this.setB1 = 'normal';
      this.setB2 = 'normal';
      this.setB3 = 'normal';
      this.setB4 = 'bold';
      this.hover1 = 0;
      this.hover2 = 0;
      this.hover3 = 0;
      this.hover4 = 1;
    },
    changeMine: function() {
      this.myform = true;
      this.myset = false;
      this.mineA = true;
      this.detailA = false;
      this.myformC = '#555964';
      this.mysetC = '#83868E';
      this.formL = 62;
      this.setL = 51;
    },
    changeSet: function() {
      this.myform = false;
      this.myset = true;
      this.mineA = false;
      this.detailA = true;
      this.myformC = '#83868E';
      this.mysetC = '#555964';
      this.formL = 51;
      this.setL = 62;
    },
    //个人中心
    mineForm: function() {
      var that = this;
      let token = window.sessionStorage.getItem('token');
      if(token == null) {
        that.$router.push({ path: '/logIn' });
      }else {
        that.$router.push({ path: '/mineForm' });
      }
    },
    //登录
    login: function() {
      var that = this;
      let token = window.sessionStorage.getItem('token');
      // console.log(token);
      if(token == null) {
        that.$router.push({ path: '/logIn' });
      }else {
        that.$message('你已登录！');
        return false
      }
    },
    //登出
    logout: function () {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      axios.get( that.add + '/api/v1.0/auth/logout',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        console.log(res);
        if (res.data.errcode == 200) {
          that.logIn = true;
          that.register = true;
          that.logOut = false;
          that.username = false;
          window.sessionStorage.removeItem('token'); 
          if(that.logIn == true) {
            that.$router.go(0);
            console.log('登出成功');
            return false
          }
        } else {
          console.log('登出失败');
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    //用户名
    kk: function() {
      var that = this;
      that.$router.push({ path: '/mineForm'});
    },
    //注册
    Register: function() {
      var that = this;
      that.$router.push({ path: '/register'});
    },
    //购物车
    shopCar: function() {
      var that = this;
      let token = window.sessionStorage.getItem('token');
      // console.log(token);
      if(token == null) {
        that.$router.push({ path: '/logIn' });
      }else {
        that.$router.push({ path: '/checkShop' });
        return false
      }
    },
    //加盟合作
    league: function() {
      var that = this;
      that.$message({
        message: '暂未开通，敬请期待！',
        type: 'warning'
      });
    },
    //媒体采访
    media: function() {
      var that = this;
      that.$message({
        message: '暂未开通，敬请期待！',
        type: 'warning'
      });
    },
    //玩转手机版
    wz_mobile: function() {
      var that = this;
      that.$message({
        message: '暂未开通，敬请期待！',
        type: 'warning'
      });
    }
  }
}      
</script>

