<template>
  <div class="logIn">
    <!-- loading开始 -->
    <div class="loading" v-show="loading">
      <img class="loading_ico" src="../../images/loading.png" alt="img">
    </div>
    <!-- loading结束 -->
    <div class="logIn_head_box">
      <div class="logIn_head">
        <router-link to="/"><img class="logo_img" src="../../images/wz_logo.png" alt="image"></router-link>
        <p class="logIn_word">欢迎来到玩转文化传媒，请登录<router-link to="/register"><span class="special">注册</span></router-link></p>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="logIn_box">
      <div class="logIn_container">
        <h3>用户登录</h3>
        <div class="phone_box">
          <span class="mb">手机号</span>
          <input type="text" class="mobilePhone" id="mobilePhone" placeholder="请输入手机号码" v-model="mobilephone">
          <transition name="shake">
            <span v-show="phoneAlert" class="phone_alert">请输入有效的手机号!</span>
          </transition>
        </div>
        <div class="phone_box">
          <span class="mm">密码</span>
          <input type="password" class="" id="password" placeholder="请输入密码" v-model="pwd">
          <transition name="shake">
            <span v-show="pwdAlert" class="phone_alert">密码格式不正确！</span>
          </transition>
        </div>
        <div class="choose_box">
          <input type="checkbox" name="auto" value="auto"/><span class="autoLog">下次自动登录</span><router-link to="/forgetPw"><span class="forgetPassword">忘记密码</span></router-link>
        </div>
        <div class="btn_box">
          <button v-on:click="logIn(mobilephone, pwd)" class="btn_log">登录</button>
        </div>
        <p class="thirdParty">使用第三方登录</p>
      </div>
    </div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import axios from 'axios';
  import footerBottom from '../index/footer';
  import { Message } from 'element-ui';
  var crypto = require('crypto');
  export default {
    name: 'logIn',
    data () {
      return {
        add: this.GLOBAL.address,
        mobilephone: '',
        pwd: '',
        phoneAlert: false,
        pwdAlert: false,
        loading: false,
      }
    },
    components: {
      footerBottom
    },
    watch: {
      mobilephone: function() {
        var that = this;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
        if (that.mobilephone !== '' && mb.test(that.mobilephone)) {
          that.phoneAlert = false;
        }
      },
      pwd: function() {
        var that = this;
        if (that.pwd.length>7 && that.pwd.length<17 && that.pwd !== '') {
          that.pwdAlert = false;
        }
      }
    },
    methods: {
      //登录
      logIn: function(mobilephone, pwd) {
        var that = this;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/; 
        if(!mb.test(that.mobilephone) || that.mobilephone == '') { 
          that.phoneAlert = true;
          return false
        }
        if(that.pwd.length<8 || that.pwd.length>16 || that.pwd == '') {
          that.pwdAlert = true;
          return false
        }
        that.loading = true;
        axios.get( that.add + '/api/v1.0/auth/salt',{
          params: {
            mobilephone: this.mobilephone
          }
        }).then(function (response) {
          // console.log(that);
          // console.log(pwd);
          //获取hash值
          crypto.pbkdf2(pwd, response.data.data, 4096, 256, function (err, hash) {
            // console.log(hash.toString('hex'));
            var Hash = hash.toString('hex')
            axios({
              method: 'post',
              url: that.add +'/api/v1.0/auth/login',
              data: `mobilephone=${mobilephone}&pwd=${Hash}`
            }).then(function (response) {
              console.log(response.data);
              if(response.data.errcode == 200){
                const token = response.data.data.token;
                // console.log(token);
                window.sessionStorage.setItem('token', token);
                // var kk = window.sessionStorage.getItem('token');
                // console.log(kk);
                //window.localStorage.setItem('token', token);
                window.location.pathname = '/';
                console.log('登录成功');
                //登录成功提示
                that.loading = false;
                that.$message({
                  message: '欢迎您来到玩转文化！！',
                  type: 'success'
                });
                that.$router.push({ name: 'index', params: {token: token}});
                return false
              }
              if(response.data.errcode == 404) {
                console.log('登录失败');
                that.loading = false;
                that.mobilephone = '';
                that.pwd = '';
                that.$message.error('用户名或密码不正确！');
              }

            }).catch(function (error) {
              console.log(error);
            });  
            if (err) {
              throw err;
            }
          })
        }).catch(function (error) {
          console.log(error);
        });  
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  input {
    border: none;
    outline:medium;
    font-size: 16px;
  }
  .shake-enter-active {
    animation: shake .5s;
  }
  .shake-leave-active {
    transition: all 0.3s ease;
    opacity: 0;
  }
  /*--------loading加载页面---------*/
  .loading {
    z-index: 999;
    width: 450px;
    height: 412px;
    line-height: 412px;
    text-align: center;
    background-color: rgba(255,255,255,0.85);
    position: absolute;
    top: 122px;
    left: 0;
    right: 0;
    margin: auto;
  }

  .loading .loading_ico {
    width: 60px;
    height: 60px;
    animation-name: rotating;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }

  @keyframes rotating {
    from {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(120deg)
    }
  }
  /*--------loading加载页面---------*/
  /*--------页面头部---------*/

  .logIn_head_box {
    width: 100%;
    height: 70px;
    border-bottom: 2px solid #FFA000;
  }
  .logIn_head {
    margin: 0 auto;
    width: 1024px;
    height: 70px;
    line-height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .logIn_head .logo_img {
    margin-top: 6.5px;
  }

  .logIn_head .logIn_word {
    float: right;
    font-size: 16px;
    color: #555964;
  }

  .special {
    color: #FFA000;
    margin-left: 5px;
    padding-left: 5px;
    border-left: 1px solid #555964;
  }
  /*--------页面头部---------*/
  .logIn_box {
    width: 100%;
    height: 698px;
    background-color: #F2F2F2;
    padding-top: 50px;
  }

  .logIn_container {
    margin: 0 auto;
    background-color: #fff;
    width: 450px;
    height: 412px;
  }

  .logIn_container h3 {
    width: 450px;
    font-size: 18px;
    color: #555964;
    font-weight: normal;
    padding-top: 36px;
    margin-bottom: 27px;
    text-align: center;
  }

  .phone_box {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 390px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #E0E0E0;
    position: relative;
  }

  .phone_box .mb {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .mm {
    font-size: 16px;
    color: #555964;
    margin-right: 58px;
  }

  .choose_box {
    margin: 0 auto; 
    margin-top: 39px;
    width: 390px;   
  }

  .choose_box .autoLog {
    margin-left: 20px;
    font-size: 14px;
    color: #83868E;
  }

  .choose_box .forgetPassword {
    margin-left: 216px;
    font-size: 14px;
    color: #83868E;
  }

  .btn_box {
    margin: 0 auto;
    margin-top: 17px;
    width: 390px;
    height: 50px;
  }

  .btn_box .btn_log {
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    background-color: #FFA000;
    outline: none;
    border: none;
    list-style-type:none;
  }

  .btn_box .btn_log:hover {
    cursor: pointer;
    transition: all 0.5s ease;
    background-color: #FF7A00;
  }

  .thirdParty {
    margin-left: 30px;
    margin-top: 17px;
    font-size: 14px;
    color: #83868E;
  }

  .phone_box .phone_alert {
    color:#D0011B;
    font-size: 14px;
    position: absolute;
    bottom: -39px;
    left: 0;
  }
</style>



