<template>
  <div class="allForm">
    <div class="allpay_box">
      <div class="allpay_th">
        <div class="left merchandise">商品</div>
        <div class="left afterSale">售后</div>
        <div class="left total">合计</div>
        <div class="left state">状态</div>
        <div class="left operate">操作</div>
      </div>
      <div v-show="noForm" class="noForm_box">
        <div class="noForm_word">您没有下过订单哦～</div>
        <router-link to="/"><div class="lookIndex">去首页看看</div></router-link>
      </div>
      <div class="wait_box" v-show="hasForm" v-for="order in orders" :key="order">
        <div class="wait_form">
          <div class="first_box">
            <span class="time">{{order.createdDate}}</span>
            <span class="form_num">订单号：{{order._id}}</span>
          </div>
          <ul class="second_box">
            <li class="left cargo_img_box">
              <img class="cargo_img" :src=order.pictureOne alt="image">
            </li>
            <li class="left cargo_des_box">
              <p class="cargo_des1">{{order.caption}}</p>
              <p class="cargo_des2">有效期：{{order.endDate | formatDate}}</p>
              <p class="cargo_des3">地区：{{order.region}}</p>
            </li>
            <li class="left refund_box">退款</li>
            <li class="left price_box">¥{{order.totalprice}}</li>
            <li class="left state_box">{{order.state | frisco}}</li>     
            <li class="left operate_box">
              <div class="comment">评价</div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'allForm',
    data () {
      return {
        add: this.GLOBAL.address,
        noForm: false,
        hasForm: true,
        orders: [],
      }
    },
    components: {
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      that.token = token;
      axios.get(that.add + '/api/v1.0/order/list',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        console.log(res.data.data);
        that.orders = res.data.data;
        if (that.orders.length == 0) {
          that.noForm = true;
          that.hasForm = false;
        }
        // console.log(that.token);
        // that.src = res.data.data.head;
        // that.username = res.data.data.username;
        // that.nickname = res.data.data.nickname;
        // that.picked = res.data.data.sex
      })
      .catch(function (error) {
        console.log(error);
      });
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .left {
    float: left;
  }
  
  .allpay_box {
    position: relative;
  }
  /*   allpay start   */
  .allpay_th {
    width: 766px;
    height: 34px;
    margin-top: 14px;
    color: #555964;
    font-size: 14px;
    background-color: #F4F4F4;
  }

  .allpay_th .merchandise {
    width: 377px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .afterSale {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .total {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .state {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .operate {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }
  /*   allpay end   */
  /*   noForm_box start   */
  .noForm_box {
    width: 736px;
    height: 259px;
    line-height: 239px;
    text-align: center;
  }

  .noForm_word {
    width: 180px;
    height: 37px;
    margin: auto;
    color: #555964;
    font-size: 20px;
  }

  .lookIndex {
    width: 90px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    background-color: #FFA000;
    border-radius: 3px;
    color: #fff;
    font-size: 14px;
    display: inline-block;
    margin-top: 22px;
  }
  /*   noForm_box end   */
  /*    wait_box start   */
  .wait_form {
    width: 766px;
    height: 160px;
    margin-top: 10px;
    background-color: #fff;
    border: 1px solid #F4F4F4;
  }

  .wait_form .first_box {
    width: 766px;
    height: 40px;
    line-height: 40px;
    color: #555964;
    font-size: 14px;
    background-color: #F4F4F4;
  }

  .first_box .time {
    font-weight: bold;
    margin-left: 20px;
  }

  .first_box .form_num {
    margin-left: 20px;
  }

  .second_box {
    width: 766px;
    height: 120px;
    line-height: 120px;
  }

  .cargo_img_box {
    margin-right: 30px;
    height: 120px;
  }

  .cargo_img {
    width: 80px;
    height: 80px;
    margin-top: 20px;
    margin-left: 20px;
  }

  .cargo_des_box {
    position: relative;
    width: 243px;
    height: 66px;
    margin-top: 22px;
    line-height: 25px;
  }

  .cargo_des_box .cargo_des1 {
    color: #555964;
    font-size: 16px;
  }

  .cargo_des_box .cargo_des2 {
    color: #83868E;
    font-size: 14px;
  }

  .cargo_des_box .cargo_des3 {
    color: #83868E;
    font-size: 14px;
  }

  .refund_box {
    width: 100px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .price_box {
    width: 100px;
    height: 120px;
    color: #555964;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .state_box {
    width: 96px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .operate_box {
    width: 90px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    position: relative;
  }

  .operate_box .comment {
    position: absolute;
    top: 46px;
    left: 20px;
    width: 50px;
    height: 28px;
    line-height: 29px;
    color: #555964;
    font-size: 14px;
    border-radius: 3px;
    border: 1px solid #E0E0E0;
  }
  /*    wait_box end   */
</style>



