          that.realAlert = true;
          that.$message.error('添加地址失败！');
          return false
        }
        if(that.district == null) {
          that.placeAlert = true;
          that.$message.error('添加地址失败！');
          return false
        }
        if(that.street == '') {
          that.addAlert = true;
          that.$message.error('添加地址失败！');
          return false
        }
