<template>
  <div class="meal">
    <div class="meal_box" v-for="item in items" :key="item">
      <img class="meal_img" @click="detail(item)" :src=item.pictureOne alt="img">
      <div class="right_box">
        <p class="meal_title">{{item.caption}}</p>
        <p class="meal_des">{{item.description}}</p>
        <div class="meal_price_box">
          <p class="market_price_word">市场价：<span class="market_price">¥{{item.price}}</span></p>
          <p class="enjoy_price_word">体验价：<span class="enjoy_price">¥{{item.discount}}</span><span class="special">元/年</span></p>
        </div>
        <div class="phoneTalk" @click="tel(item)">电话咨询</div>
        <div class="addIn" @click="addGoods(item)">
          <img class="addIn_img" src="../../images/shopcarWhite.png" alt="img"><span class="addIn_word">加入购物车</span></div>
        </div>
      </div>
    </div>
  </template>

  <script>
    import axios from 'axios'
    export default {
      name: 'meal',
      data () {
        return {
          add: this.GLOBAL.address,
          items: [],
          shops: [],
        }
      },
      components: {
      },
      watch: {
        shops: function() {
          var that = this;
          var shops = that.shops;
          console.log(shops.length,'hah');
        }
      },
      mounted: function() {
        var that = this;
        axios.get(that.add + '/api/v1.0/goods/list',{
        })
        .then(function (res) {
          var a = res.data.data;
          var b = a.filter(function(ele,index,arr){
            // console.log(ele);
            // that.items.push(ele);
            return ele.type == '工商服务-特殊';
          });
          that.items = b;
      })
        .catch(function (error) {
          console.log(error);
        });
      },
      methods: {
      //加入购物车
      addGoods: function(item) {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        // console.log(item._id, token);
        axios({
          method: 'post',
          url: that.add + '/api/v1.0/order/shoppingcart_add',
          data: `objectId=${item._id}&token=${token}`
        })
        .then(function (res) {
          if (res.data.errcode == 404) {
            that.$message.error('商品添加失败，请先登录！');
          } else {
            axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
              params: {
                token: token
              }
            })
            .then(function (res) {
              if (res.data.errcode == 200) {
                that.shops = res.data.data;
                that.$message({
                  message: '商品添加成功！',
                type: 'success'
              });              
                that.$router.push({path: '/checkShop'});
              } else {
                that.$message.error('商品添加失败!');
              }
              // console.log(res.data.data);
              //console.log(that.shops.length);
            })
            .catch(function (error) {
              console.log(error);
            });
          }
        })
        .catch(function (error) {
          console.log(error);
        });
      },
      //电话咨询
      tel: function(item) {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        axios({
          method: 'post',
          url: that.add + '/api/v1.0/goods/callme',
          data: `objectId=${item._id}&mobilephone=${'15900901007'}`
        })
        .then(function (res) {
          console.log(res);
        })
        .catch(function (error) {
          console.log(error);
        });
      },
      //查看详情
      detail: function(item) {
        var that = this;
        that.$router.push({ name: 'detail', params: { id: item._id}});
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .meal {
    width: 1024px;
    height: 330px;
    margin: 0 auto;
    margin-top: 30px;
  }
  .meal_box {
    width: 1024px;
    height: 330px;
    border: 1px solid #EBEBEB;
  }
  .meal_img {
    width: 290px;
    height: 290px;
    float: left;
    margin-left: 20px;
    margin-top: 20px;
  }
  .meal_img:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  .right_box {
    margin-left: 20px;
    margin-top: 20px;
    width: 692px;
    height: 290px;
    float: left;
  }
  .meal_title {
    margin-top: 10px;
    font-size: 24px;
    color: #555964;
    margin-bottom: 10px;
  }
  .meal_des {
    width: 660px;
    font-size: 14px;
    color: #83868E;
    margin-bottom: 12px;
  }
  .meal_price_box {
    width: 692px;
    height: 78px;
    background-color: #FBFBFB;
    color: #83868E;
    font-size: 12px;
  }
  .market_price_word {
    padding-top: 17px;
    margin-left: 10px;
  }
  .market_price {
    margin-left: 10px;
    text-decoration: line-through;
  }
  .enjoy_price_word {
    margin-left: 10px;
  }
  .enjoy_price {
    color: #E4393C;
    font-size: 28px;
    font-weight: bold;
  }
  .special {
    color: #555964;
    font-size: 14px;
  }
  .phoneTalk {
    float: left;
    width: 130px;
    height: 40px;
    line-height: 41px;
    text-align: center;
    background-color: rgba(255,160,0,0.2);
    color: #F7941E;
    font-size: 14px;
    margin-top: 20px;
    margin-left: 5px;
  }
  .addIn {
    float: left;
    width: 170px;
    height: 40px;
    line-height: 41px;
    text-align: center;
    background-color: #FFA000;
    color: #fff;
    font-size: 14px;
    margin-top: 20px;
    margin-left: 20px;
    position: relative;
  }
  .addIn:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  .addIn_img {
    position: absolute;
    top: 12px;
    left: 39px;
  }
  .addIn_word {
    position: absolute;
    top: 0px;
    left: 60px;
  }
</style>



