<template>
  <div class="list">
    <div class="knowledge_head">
      <img class="knowledge_img" src="../../images/tax.png" alt="">
      <div class="knowledge_headline">财税服务</div>
      <p class="knowledge_des">更多服务更多服务更多服务</p>
    </div>
    <div class="good_box">
      <div class="good" v-for="item in items" :key="item" @click="detail(item)">
        <img class="good_img" :src=item.pictureOne alt="img">
        <div class="good_des">{{item.caption}}</div>
        <div class="good_hover">
          <span class="detail">查看详情</span>
          <img class="detail_img" src="../../images/seeDetail.png" alt="img"></div>
        </div>
      </div>
    </div>
  </template>

  <script>
    import axios from 'axios'
    export default {
      name: 'list',
      data () {
        return {
          add: this.GLOBAL.address,
          items: [],
        }
      },
      components: {
      },
      mounted: function() {
        window.scroll(0,0);
        var that = this;
        axios.get( that.add + '/api/v1.0/goods/list',{
        })
        .then(function (res) {
        var a = res.data.data;
        // console.log(a);
        var b = a.filter(function(ele,index,arr){
            // console.log(ele);
            return ele.type == '财税服务-普通';   
        });
        that.items = b;
        console.log(b);
      })
        .catch(function (error) {
          console.log(error);
        });
      },
      methods: {
        detail: function(item) {
          var that = this;
          that.$router.push({ name: 'detail', params: { id: item._id }});
        }
      }
    }
  </script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .knowledge_head {
      width: 1024px;
      padding-top: 36px;
      margin: 0 auto;
      text-align: center;
    }
    .knowledge_head .knowledge_img{
      margin-bottom: 7px;
    }

    .knowledge_head .knowledge_headline{
      width: 284px;
      height: 33px;
      margin: 0 auto;
      background: url(../../images/style.png) no-repeat;
      background-position: 0% 50%;
      font-size: 24px;
      font-weight: bold;
      color: #FFA000;
    }

    .knowledge_head .knowledge_des {
      font-size: 16px;
      color: #83868E;
      margin-bottom: 20px;
    }
    /*------good_box start------*/
    .good_box {
      width: 1024px;
      height: 1000px;
      margin: 0 auto;
      /* background-color: #EECE7C; */
    }
    .good {
      float: left;
      width: 240px;
      height: 290px;
      color: #83868E;
      font-size: 16px;
      border: 2px solid #fff;
      position: relative;
      margin-right: 12px;
      margin-bottom: 36px;
    }
    .good:hover {
      cursor: pointer;
      transition: all 0.3s ease-out;
      border: 2px solid #FFA000;
    }
    .good:hover .good_hover{
      display: block;
    }
    .good_img {
      width: 240px;
      height: 240px;
    }
    .good_des {
      width: 240px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background-color: #FAFAFA;
      position: absolute;
      bottom: 0;
    }
    .good_hover {
      width: 240px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      color: #fff;
      background-color: #FFA000;
      z-index: 99;
      position: absolute;
      bottom: 0;
      display: none;
    }
    .detail {
      float: left;
      margin-left: 76px;
    }
    .detail_img {
      margin-left: 5px;
      margin-top: 14px;
      float: left;
    }
    /*------good_box end------*/
  </style>



