<template>
  <div class="checkShopping">
    <publicHeader v-bind:num="numValue"></publicHeader>
    <div class="logIn_head_box">
      <div class="logIn_head">
        <router-link to="/"><img class="logo_img" src="../../images/wz_logo.png" alt="image"></router-link>
        <img class="shopfirst" src="../../images/shopfirst.png" alt="image">
      </div>
    </div>
    <div class="nocargo" v-show="goshopping">
      <img class="nocargo_img" src="../../images/car.png" alt="img">
      <p class="nocargo_word">您的购物车暂无商品，去看看心仪的产品吧～</p>
      <router-link to="/index"><div class="gogo">随便逛逛</div></router-link>
    </div>
    <div v-show="myshop_car">
      <div class="myshop_headline">我的购物车</div>
      <div class="myshop_box">
        <ul class="myshop_th">
          <li class="left Ach"><input type="checkbox" class="allchoose" name="allchoose" v-model="allChecked"/>全选</li>
          <li class="left cargoDetail">商品信息</li>
          <li class="left service_provider">服务商</li>
          <li class="left price">单价</li>
          <li class="left number">数量</li>
          <li class="left subtotal">小计</li>
          <li class="left operate">操作</li>
        </ul>
        <div class="clearfix"></div>
        <div v-for="item in shops" :key="item">
          <div class="cargo_type_box">
            <input type="checkbox" class="cargo_type" name="cargo_type" :value="item" value="cargo_type" v-model="checked"/>{{item.category}}
          </div>
          <ul class="cargo_detail_box">
            <li class="left ch_one_box">
              <input type="checkbox" class="ch_one" name="ch_one" :value="item" v-model="checked"/>
            </li>
            <li class="left cargo_img_box">
              <img class="cargo_img" :src=item.pictureOne alt="image">
            </li>
            <li class="left cargo_des_box">
              <p class="cargo_des1">{{item.caption}}</p>
              <p class="cargo_des2">有效期：{{item.endDate | formatDate}}</p>
              <p class="cargo_des3">地区：{{item.region}}</p>
            </li>
            <li class="left">
              <div class="servive_type">{{item.vender}}</div>
            </li>
            <li class="left cargo_price">¥{{item.price}}</li>
            <li class="left cargo_number_box">
              <span v-on:click="numMinus(item)" v-bind:style="{ color: Color }" class="minus">_</span>
              <span class="change_num">{{item.count}}</span>
              <span v-on:click="numPlus(item)" class="plus">+</span>
            </li>
            <li class="left cargo_subtotal">¥{{item.count*item.price}}</li>
            <li class="left rmrmrm" v-on:click="removeItem(item)">删除</li>
          </ul>
        </div>
        <ul class="total_box">
          <li class="left Ach"><input type="checkbox" class="allchoose" name="allchoose" v-model="allChecked"/>全选</li>
          <li class="left delele_ch" v-on:click="removeActive()">删除选中商品</li>
          <li class="left item_num">商品数量<span style="font-weight: bold; font-size: 24px; color: #FFA000;">{{allCount}}</span>件</li>
          <li class="left item_total">合计：<span style="font-weight: bold; font-size: 24px; color: #FFA000;">¥{{allMoney}}</span></li>
          <li class="left total_btn_box">
            <router-link :to="{ name: 'confirmOrder', params: { allCount: this.allCount, allMoney: this.allMoney, checked: this.checked }}">
              <button class="left total_btn" v-bind:style="{ backgroundColor: btnColor, cursor: activeSquare }" v-if="hasM">结算</button>
              <button class="left total_btn" v-bind:style="{ backgroundColor: btnColor }" v-else disabled>结算</button>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import publicHeader from '../index/header'
  import footerBottom from '../index/footer'
  export default {
    name: 'checkShopping',
    data () {
      return {
        add: this.GLOBAL.address,
        shops: [],
        myshop_car: false,
        goshopping: true,
        item_subtotal: 0,
        sum: 0,
        sum1: 0,
        allCount: 0,
        allMoney: 0,
        Color: '#555964',
        btnColor: '#D8D8D8',
        activeSquare: 'auto',
        token:'',
        hasM: false,
        checked: [],
        numValue: 0,
      }
    },
    components: {
      publicHeader,
      footerBottom
    },
    //*******导航钩子*********//
    beforeRouteEnter (to, from, next) {
      // 通过 `vm` 访问组件实例
      next(vm => {
        var that = vm;
        var token = window.sessionStorage.getItem('token');
        that.checked = [];
        axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
          params: {
            token: token
          }
        }).then(function (res) {
          if(res.data.errcode == 200) {
            that.shops = res.data.data;
            that.numValue = that.shops.length;
            console.log(res);
            console.log(that.shops, that.numValue);    
            if(that.shops.length !== 0) {
              that.goshopping = false;
              that.myshop_car = true;
            } else {
              that.goshopping = true;
              that.myshop_car = false;
            }        
          } else {
            return false
          }
        })
        .catch(function (error) {
          console.log(error);
        });
      })
    },
    // mounted: function() {
    //   var that = this;
    //   var token = window.sessionStorage.getItem('token');
    //   that.token = token;
    //   axios.get(that.add + '/api/v1.0/order/shoppingcart_list',{
    //     params: {
    //       token: token
    //     }
    //   })
    //   .then(function (res) {
    //     that.shops = res.data.data;
    //     // console.log(res.data.data);
    //     console.log(that.shops);
    //     if(that.shops.length !== 0) {
    //       that.goshopping = false;
    //       that.myshop_car = true;
    //     }
    //   })
    //   .catch(function (error) {
    //     console.log(error);
    //   });
    // },
    watch: {
      checked: function() {
        var that = this;
        var check = that.checked;
        var sum = that.sum;
        var sum1 = that.sum1;
        if (that.checked.length === 0) {
          that.hasM = false;
          that.btnColor = '#D8D8D8';
          that.activeSquare = 'auto';
          that.allCount = 0;
          that.allMoney = 0;
        } else {
          for (var i = 0; i < check.length; i++) {
            sum += parseInt(check[i].count);
            sum1 += parseInt(check[i].count*check[i].price);
          }
          that.allCount = sum;
          that.allMoney = sum1;
          that.hasM = true;
          that.btnColor = '#FFA000';
          that.activeSquare = 'pointer';
        }
      },
      allCount: function() {
        var that = this;
        var allCount = that.allCount;
        // console.log(allCount);
        if (allCount == 0) {
          that.hasM = false;
          that.btnColor = '#D8D8D8';
          that.activeSquare = 'auto';
        }
      }
    },
    computed: {
      //全选反选商品
      allChecked: {
        get: function() {
         return this.checkedCount == this.shops.length;
       },
       set: function(value) {
        if (value) {
          this.hasM = true;
          this.btnColor = '#FFA000';
          this.activeSquare = 'pointer';
          this.checked = this.shops.map(function(item) {
            return item
          })
        } else {
          this.hasM = false;
          this.btnColor = '#D8D8D8';
          this.activeSquare = 'auto';
          this.checked = [];
        }
      }
    },
    checkedCount: {
      get: function() {
        return this.checked.length;
      }
    },
  },
  methods: {
  showMsgFromChild: function(data) {
    var that = this;
    console.log(data);
  },
  // 删除单个商品
  removeItem: function(item) {
    var that = this;
    var token = window.sessionStorage.getItem('token');
    that.$confirm('是否确认删除所选商品', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      axios.get(that.add + '/api/v1.0/order/shoppingcart_del',{
        params: {
          token: token,
          objectId: item._id
        }
      })
      .then(function (res) {
        that.shops = res.data.data;
        // console.log(res.data.data);
        // console.log(that.shops);
        that.$router.go(0);
      })
      .catch(function (error) {
        console.log(error);
      });
    }).catch(() => {     
    });
  },
  // 删除所选商品
  removeActive: function() {
    return false
    var that  = this;
    if (that.checked.length == 0) {
      that.$alert('请先勾选要删除的商品！', '提示', {
        confirmButtonText: '确定',
        // callback: action => {
        //   this.$message({
        //     type: 'info',
        //     message: `action: ${ action }`
        //   });
        // }
      });
    } else {
      that.$confirm('是否确认删除所选商品', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // for (var i = 0; i < that.checked.length; i++) {
        //   that.allCount = that.allCount - that.checked[i].changeNum;
        //   that.allMoney = that.allMoney - that.checked[i].subtotal;
        //   that.checked[i].Rm = false;
        //   delete that.checked[i]
        // }
      }).catch(() => {     
      });
    }
  },
  // 商品数量加
  numPlus: function(item) {
    var token = window.sessionStorage.getItem('token');
    var that = this;
    var check = that.checked;
    var sum = that.sum;
    var sum1 = that.sum1;
    item.count++;
    // console.log(check);
    axios({
      method: 'post',
          url: that.add + '/api/v1.0/order/shoppingcart_update',
          data: `objectId=${item._id}&token=${token}&count=${item.count}`
        }).then(function(res){
          console.log(res);
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
        });   
        if(that.allCount > 0) {
          for (var i = 0; i < check.length; i++) {
            sum += parseInt(check[i].count);
            sum1 += parseInt(check[i].count*check[i].price);
          }
          that.allCount = sum;
          that.allMoney = sum1;
        }
      },
  // 商品数量减
  numMinus: function(item) {
    var token = window.sessionStorage.getItem('token');
    var that = this;
    var sum = that.sum;
    var sum1 = that.sum1;
    var check = that.checked;
    item.count--;
    if (item.count < 1) {
      this.$alert('商品数量不能为0', '警告', {
        confirmButtonText: '确定',
        callback: action => {
          item.count = 1;
        }
      });
      return false
    }
    else {
      // item.count--;
      axios({
        method: 'post',
          url: that.add + '/api/v1.0/order/shoppingcart_update',
          data: `objectId=${item._id}&token=${token}&count=${item.count}`
        }).then(function(res){
          console.log(res);
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
        });
        if(that.allCount > 0) {
          for (var i = 0; i < check.length; i++) {
            sum += parseInt(check[i].count);
            sum1 += parseInt(check[i].count*check[i].price);
          }
          that.allCount = sum;
          that.allMoney = sum1;
        }
        // if(that.allCount > 0) {
        //   that.allCount--;
        //   that.allMoney = that.allMoney - item.price;
        // }
      }
    },
  }
}
</script>

