// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import moment from 'moment'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import global_ from './Global'
Vue.prototype.GLOBAL = global_
Vue.use(ElementUI);
// Vue.prototype.$http = axios
Vue.config.productionTip = false;

// 时间戳格式转换
Vue.filter('formatDate', function (value) {
	var newDate = new Date();
	newDate.setTime(value * 1000);
	var kk = newDate.toLocaleDateString()
	var middleState = kk.split("");
	middleState.splice(4,1,"年");
	middleState.splice(6,1,"月")
	middleState.splice(middleState.length,0,"日")
	var middleState2 = middleState.toString().replace(/,/g,'')
	return middleState2
})

Vue.filter('frisco', function (value) {
    if(value == 0) {
    	var hh = '待付款'
    	return hh
    }
    if (value == 1) {
    	var aa = '交易关闭'
    	return aa
    }
    if (value == 2) {
    	var ee = '交易成功'
    	return ee
    }
    // return middleState2
})
/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	template: '<App/>',
	components: { App }
})



