<template>
  <div class="forgetPw">
    <div class="logIn_head_box">
      <div class="logIn_head">
        <router-link to="/"><img class="logo_img" src="../../images/wz_logo.png" alt="image"></router-link>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="logIn_box">
      <div class="logIn_container">
        <h3>重置登录密码</h3>
        <div class="phone_box">
          <span class="mb">手机号</span><input type="text" class="mobilePhone" id="mobilePhone" placeholder="请输入手机号码" v-model="user.mobilephone">
        </div>
        <div class="yzm_box">
          <span class="yzm">验证码</span><input type="text" class="autoCode" id="autoCode" placeholder="请输入验证码" v-model="user.vercode">
          <button v-on:click="hasYzm" class="hasYzm" v-if="hYzm">{{Yzm}}</button>
          <button v-on:click="hasYzm" class="hasYzm" style="background-color: #C8C8C8;" v-else disabled>{{Yzm1}}</button>
        </div>
        <div class="phone_box">
          <span class="newMm">新密码</span><input type="password" class="" id="setPassword" placeholder="请设置新密码" v-model="user.setPassword">
        </div>
        <div class="phone_box">
          <span class="confirmMm">确认密码</span><input type="password" class="" id="confirmPassword" placeholder="请确认新密码" v-model="user.confirmPassword">
        </div>
        <div class="btn_box">
          <button v-on:click="logIn" class="btn_log">确认</button>
        </div>
      </div>
    </div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import footerBottom from '../index/footer'
  export default {
    name: 'forgetPw',
    data () {
      return {
        user: {
          mobilephone: '',
          setPassword: '',
          confirmPassword: '',
          vercode: '',
        },
        hYzm: true,
        Yzm: '点击获取',
        Yzm1: ''
      }
    },
    components: {
      footerBottom
    },
    methods: {
      logIn: function() {
        var that = this;
        var user = that.user;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/; 
        if(user.mobilephone == '') { 
          alert('手机号码不能为空！'); 
          return false
        }
        if(!mb.test(user.mobilephone)) { 
          alert('请输入有效的手机号码！'); 
          return false
        }
        if(user.setPassword == '') { 
          alert('设置密码不能为空！'); 
          return false
        }
        if(user.setPassword.length<8 || user.setPassword.length>16) {
          alert('密码长度需在8到16位之间！'); 
          return false
        }
        if(user.confirmPassword == '') { 
          alert('确认密码不能为空！'); 
          return false
        }
        if(user.confirmPassword !== user.setPassword) {
          alert('设置密码与确认密码不一致！'); 
          return false
        }
        console.log(this.user);
      },
      hasYzm: function() {
        var that = this;
        var user = that.user;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/; 
        var sec = 60;
        if (user.mobilephone == '' || !mb.test(user.mobilephone)) {
          alert('请先输入有效的手机号！')
        }else {
          for(let  i=0; i<=60; i++){
           window.setTimeout(function(){
             if (sec != 0) {
              that.hYzm = false;
              that.Yzm1 =   sec + "秒后重发验证" ;
              sec--;
            } else {
             sec = 60;//如果倒计时结束就让  获取验证码显示出来
             that.hYzm = true;
             that.Yzm = '获取验证码';
           }
         }, i * 1000)
         }
       }
        // that.Yzm = '60s'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  input {
    border: none;
    outline:medium;
    font-size: 16px;
  }

  button {
    outline: none;
    border: none;
    list-style-type:none;
  }
  /*--------页面头部---------*/
  .logIn_head_box {
    width: 100%;
    height: 70px;
    border-bottom: 2px solid #FFA000;
  }
  .logIn_head {
    margin: 0 auto;
    width: 1024px;
    height: 70px;
    line-height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .logIn_head .logo_img {
    margin-top: 6.5px;
  }

  .logIn_head .logIn_word {
    float: right;
    font-size: 16px;
    color: #555964;
  }

  .special {
    color: #FFA000;
    margin-left: 5px;
    padding-left: 5px;
    border-left: 1px solid #555964;
  }
  /*--------页面头部---------*/
  .logIn_box {
    width: 100%;
    height: 698px;
    background-color: #F2F2F2;
    padding-top: 50px;
  }

  .logIn_container {
    margin: 0 auto;
    background-color: #fff;
    width: 450px;
    height: 478px;
  }

  .logIn_container h3 {
    width: 450px;
    font-size: 18px;
    color: #555964;
    font-weight: normal;
    padding-top: 36px;
    margin-bottom: 27px;
    text-align: center;
  }

  .phone_box {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 390px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #E0E0E0;
    position: relative;
  }

  .yzm_box {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 390px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #E0E0E0;
    position: relative;
  }

  .yzm_box .hasYzm {
    height: 36px;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    font-size: 14px;
    background-color: #4990E2;
    position: absolute;
    top: 6px;
    right: 0px;
    padding-left: 5px;
    padding-right: 5px;
    cursor: pointer;
  }

  .phone_box .mb {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .mm {
    font-size: 16px;
    color: #555964;
    margin-right: 58px;
  }

  .yzm {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .newMm {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .confirmMm {
    font-size: 16px;
    color: #555964;
    margin-right: 26px;
  }

  .choose_box {
    margin: 0 auto; 
    margin-top: 39px;
    width: 390px;   
  }

  .choose_box .autoLog {
    margin-left: 20px;
    font-size: 14px;
    color: #83868E;
  }

  .choose_box .forgetPassword {
    margin-left: 216px;
    font-size: 14px;
    color: #83868E;
  }

  .btn_box {
    margin: 0 auto;
    margin-top: 39px;
    width: 390px;
    height: 50px;
  }

  .btn_box .btn_log {
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    background-color: #FFA000;
    cursor: pointer;
  }

  .thirdParty {
    margin-left: 30px;
    margin-top: 17px;
    font-size: 14px;
    color: #83868E;
  }
</style>



