<template>
  <div class="headerTop">
    <div class="header_first">
      <div class="header_first_box">
        <ul class="header_first_list">
          <router-link to="/index"><li class="list_left">首页</li></router-link>
          <li class="list_left" @click="league()">加盟合作</li>
          <li class="list_left" @click="media()">媒体采访</li>
          <img style="float: left; margin-top: 8px; margin-right: 2px;" src="../../images/mobilephone.png" alt="img">
          <li class="list_left" @click="wz_mobile()">玩转手机版</li>
          <li class="list_right" @click="shopCar()">购物车{{num}}件</li>
          <img style="float: right; margin-top: 8px; margin-right: 2px;" src="../../images/shopcar.png" alt="img">
          <li v-show="logOut"class="list_right" @click="logout()">退出</li>
          <li v-show="username"class="list_right" @click="kk()">hi,{{info.username}}</li>
          <li v-show="logIn" class="list_right" @click="login()">登录</li>
          <li v-show="register" class="list_right" @click="Register()">注册</li>
          <li class="list_right" @click="mineForm()">个人中心</li>
        </ul>
      </div>
    </div>
    <div v-show="second" class="header_second">
      <div class="header_second_box">
        <img class="wz_logo" src="../../images/wz_logo.png" alt="wz_logo">
        <ul class="header_second_list">
          <li class="slist">地方政策</li>
          <li class="slist">文创空间</li>
          <li class="slist">知识库</li>
          <router-link to="/tax"><li class="slist">财税服务</li></router-link>
          <router-link to="/business"><li class="slist">工商服务</li></router-link>
          <router-link to="/layer"><li class="slist">法律维权</li></router-link>
          <router-link to="/index"><li class="slist">首页</li></router-link>
        </ul>
      </div>
    </div>
    <div class="clearfix"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'headerTop',
    //props传值
    props: ['num'],
    data () {
      return {
        add: this.GLOBAL.address,
        logIn: true,
        logOut: false,
        register: true,
        username: false,
        info: {},
        second: true,
        num: 0,
      }
    },
    created: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      // that.allCount = that.$route.params.allCount;
      //console.log(token);
      axios.get( that.add + '/api/v1.0/user/userinfo',{
        params: {
          token: token
        }
      }).then(function (res) {
        //console.log(res);
        if(res.data.errcode == 404) {
          console.log('token失效');
          // that.$router.push({ path: '/logIn' });
        } else {
          that.info = res.data.data;
          that.logIn = false;
          that.register = false;
          that.logOut = true;
          that.username = true;
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      //购物车页面隐藏二级页面
      if (that.$router.history.current.name == 'checkShopping' || that.$router.history.current.name == 'confirmOrder' || that.$router.history.current.name == 'orderSuccess') {
        that.second = false;
      }
      axios.get( that.add + '/api/v1.0/order/shoppingcart_list',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        var number = res.data.data;
        //console.log(number.length);
        that.num = number.length;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      //个人中心
      mineForm: function() {
        var that = this;
        let token = window.sessionStorage.getItem('token');
        if(token == null) {
          that.$router.push({ path: '/logIn' });
        }else {
          that.$router.push({ path: '/mineForm' });
        }
      },
      //登录
      login: function() {
        var that = this;
        let token = window.sessionStorage.getItem('token');
        // console.log(token);
        if(token == null) {
          that.$router.push({ path: '/logIn' });
        }else {
          that.$message('你已登录！');
          return false
        }
      },
      //登出
      logout: function () {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        axios.get( that.add + '/api/v1.0/auth/logout',{
          params: {
            token: token
          }
        })
        .then(function (res) {
          console.log(res);
          if (res.data.errcode == 200) {
            that.logIn = true;
            that.register = true;
            that.logOut = false;
            that.username = false;
            window.sessionStorage.removeItem('token'); 
            if(that.logIn == true) {
              that.$router.go(0);
              console.log('登出成功');
              return false
            }
          } else {
            console.log('登出失败');
          }
        })
        .catch(function (error) {
          console.log(error);
        });
      },
      //用户名
      kk: function() {
        var that = this;
        that.$router.push({ path: '/mineForm'});
      },
      //注册
      Register: function() {
        var that = this;
        that.$router.push({ path: '/register'});
      },
      //购物车
      shopCar: function() {
        var that = this;
        let token = window.sessionStorage.getItem('token');
        // console.log(token);
        if(token == null) {
          that.$router.push({ path: '/logIn' });
        }else {
          that.$router.push({ path: '/checkShop' });
          return false
        }
      },
      //加盟合作
      league: function() {
        var that = this;
        that.$message({
          message: '暂未开通，敬请期待！',
          type: 'warning'
        });
      },
      //媒体采访
      media: function() {
        var that = this;
        that.$message({
          message: '暂未开通，敬请期待！',
          type: 'warning'
        });
      },
      //玩转手机版
      wz_mobile: function() {
        var that = this;
        that.$message({
          message: '暂未开通，敬请期待！',
          type: 'warning'
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul {
    list-style-type: none;
  }

  a {
    text-decoration: none;
    color: #555964;
  }

  .headerTop {
    width: 100%;
    /*height: 100px;*/
    margin-bottom: 2px;
  }
  /*-------header_first_box start------*/
  .headerTop .header_first {
    width: 100%;
    height: 30px;
    background-color: #F8F8F8;
    text-align: center;
  }

  .headerTop .header_first .header_first_box {
    margin: 0 auto;
    width: 1024px;
    height: 30px;
    background-color: #F8F8F8;
  }

  .header_first_box .header_first_list {
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #83868E;
    font-size: 12px;
  }

  .header_first_box .header_first_list .list_left {
    margin-right: 20px;
    float: left;
  }

  .header_first_box .header_first_list .list_right {
    margin-right: 20px;
    float: right;
  }

  .header_first_box .header_first_list .list_right:hover {
    /*background-color: #FFA000;*/
    color: #FFA000;
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.5s ease;
  }

  .header_first_box .header_first_list .list_left:hover {
    color: #FFA000;
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.5s ease;
  }
  /*-------header_first_box end------*/
  /*-------header_second_box start------*/
  .header_second {
    clear: both;
    width: 100%;
    height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .header_second .header_second_box {
    margin: 0 auto;
    width: 1024px;
    height: 70px;
    /*    border-bottom: 2px solid #FFA000;*/
  }

  .header_second .wz_logo {
    margin-top: 6.5px;
    width: 275px;
    height: 56px;
  }

  .header_second .header_second_list {
    height: 70px;
    line-height: 70px;
    font-size: 16px;
    color: #555964;
    float: right;
  }

  .header_second .header_second_list .slist{
    float: right;
    margin-left: 30px;
  }
  
  .header_second .header_second_list .slist:hover {
    color: #FFA000;
    transition: all 0.5s ease;
    cursor: pointer;
  }

  .router-link-active {
    color: #FFA000;
    transition: all 0.5s ease;
  }
  /*-------header_second_box end------*/
</style>



