<template>
  <div class="register">
    <div class="logIn_head_box">
      <div class="logIn_head">
        <router-link to="/"><img class="logo_img" src="../../images/wz_logo.png" alt="image"></router-link>
        <p class="logIn_word">欢迎来到玩转文化传媒，请注册<router-link to="/logIn"><span class="special">登录</span></router-link></p>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="logIn_box">
      <div class="logIn_container">
        <h3>注册</h3>
        <div class="phone_box">
          <span class="mb">手机号</span>
          <input type="text" class="mobilePhone" id="mobilePhone" placeholder="请输入手机号码" v-model="mobilephone">
          <transition name="shake">
            <span v-show="phoneAlert" class="phone_alert">请输入有效的手机号!</span>
          </transition>
        </div>
        <div class="yzm_box">
          <span class="yzm">验证码</span>
          <input type="text" class="autoCode" id="autoCode" placeholder="请输入验证码" v-model="vercode">
          <button v-on:click="hasYzm(mobilephone)" class="hasYzm" v-if="hYzm">{{Yzm}}</button>
          <button v-on:click="hasYzm" class="hasYzm" style="background-color: #C8C8C8;" v-else disabled>{{Yzm1}}</button>
          <transition name="shake">
            <span v-show="yzmAlert" class="yzm_alert">验证码输入不正确!</span>
          </transition>
        </div>
        <div class="phone_box">
          <span class="setMm">设置密码</span>
          <input type="password" class="" id="pwd" placeholder="请设置登录密码" v-model="pwd">
          <transition name="shake">
            <span v-show="pwdAlert" class="phone_alert">请输入有效的密码！密码长度需在8到16位之间</span>
          </transition>
        </div>
        <div class="phone_box">
          <span class="confirmMm">确认密码</span>
          <input type="password" class="" id="confirmPassword" placeholder="请确认登录密码" v-model="confirmPassword">
          <transition name="shake">
            <span v-show="confAlert" class="phone_alert">设置密码与确认密码不一致！</span>
          </transition>
        </div>
        <div class="choose_box">
          <input type="checkbox" name="auto" value="auto" v-model="agree"/><span class="autoLog">我已阅读并同意<span style="color: #FFA000;">《协议》</span></span>
        </div>
        <div class="btn_box">
          <button v-on:click="reg(mobilephone, pwd, vercode)" class="btn_log">注册</button>
        </div>
        <router-link to="/logIn"><p class="thirdParty">已有账号</p></router-link>
      </div>
    </div>
    <footerBottom></footerBottom>
  </div>
</template>

<script>
  import axios from 'axios'
  import footerBottom from '../index/footer'
  export default {
    name: 'register',
    data () {
      return {
        add: this.GLOBAL.address,
        errors:{},
        mobilephone: '',
        pwd: '',
        confirmPassword: '',
        vercode: '',
        agree: false,
        phoneAlert: false,
        pwdAlert: false,
        confAlert: false,
        yzmAlert: false,
        hYzm: true,
        Yzm: '点击获取',
        Yzm1: ''
      }
    },
    components: {
      footerBottom
    },
    watch: {
      mobilephone: function() {
        var that = this;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
        if (that.mobilephone !== '' && mb.test(that.mobilephone)) {
          that.phoneAlert = false;
        }
      },
      pwd: function() {
        var that = this;
        if (that.pwd.length>7 && that.pwd.length<17 && that.pwd !== '') {
          that.pwdAlert = false;
        }
      },
      confirmPassword: function() {
        var that = this;
        if (that.confirmPassword == that.pwd && that.confirmPassword !== '') {
          that.confAlert = false;
        }
      },
    },
    methods: {
      //注册
      reg: function(mobilephone, pwd, vercode) {
        var that = this;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/; 
        if(!mb.test(that.mobilephone) || that.mobilephone == '') { 
          that.phoneAlert = true;
          return false
        }
        if(that.pwd.length<8 || that.pwd.length>16 || that.pwd == '') {
          that.pwdAlert = true;
          return false
        }
        if(that.confirmPassword !== that.pwd || that.confirmPassword == '') {
          that.confAlert = true;
          return false
        }
        if(that.agree == false) {
          alert('请先同意协议！');
          return false
        }
        axios({
          method: 'post',
          // url: 'http://www.piegroups.com/api/v1.0/auth/register',
          url: that.add + '/api/v1.0/auth/register',
          data: `mobilephone=${this.mobilephone}&pwd=${this.pwd}&vercode=${this.vercode}`
        }).then(function(res){
          console.log(res);
          if(res.data.errcode == 200){
            console.log('注册成功');
            that.mobilephone = '';
            that.vercode = '';
            that.pwd = '';
            that.confirmPassword = '';
            that.agree = false;
            that.$router.push({ path: '/logIn' });
            that.$message({
              message: '注册成功请登录！',
              type: 'success'
            });
            return false
          }
          if(res.data.errcode == 404){
            console.log('注册失败');
            that.$message.error('用户名已存在！');
            that.mobilephone = '';
            that.vercode = '';
            that.pwd = '';
            that.confirmPassword = '';
            that.agree = false;
            return false
          }
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
          // this.errors=res.body;
        });       
      },
      //获取验证码
      hasYzm: function(mobilephone) {
        var that = this;
        var user = that.user;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/; 
        var sec = 60;
        if (that.mobilephone == '' || !mb.test(that.mobilephone)) {
          alert('请先输入有效的手机号！')
        }else {
          for(let  i=0; i<=60; i++){
           window.setTimeout(function(){
             if (sec != 0) {
              that.hYzm = false;
              that.Yzm1 =   sec + "秒后重发验证" ;
              sec--;
            } else {
             sec = 60;//如果倒计时结束就让  获取验证码显示出来
             that.hYzm = true;
             that.Yzm = '获取验证码';
           }
         }, i * 1000)
         }
       }
       axios.get(that.add + '/api/v1.0/auth/fetchcode',{
        params: {
          mobilephone: this.mobilephone
        }
      })
       .then(function (res) {
        console.log(res);
        if(res.status==200){
          console.log('成功');
        }else{
          console.log('失败')
        }
      })
       .catch(function (error) {
        console.log(error);
      });
     }
   }
 }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  input {
    border: none;
    outline:medium;
    font-size: 16px;
  }

  button {
    outline: none;
    border: none;
    list-style-type:none;
  }

  /*-------animation start------*/
  .shake-enter-active {
    animation: shake .5s;
  }
  .shake-leave-active {
    transition: all 0.3s ease;
    opacity: 0;
  }
  @-webkit-keyframes shake {
    from, to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    10%, 30%, 50%, 70%, 90% {
      -webkit-transform: translate3d(-7px, 0, 0);
      transform: translate3d(-7px, 0, 0);
    }

    20%, 40%, 60%, 80% {
      -webkit-transform: translate3d(7px, 0, 0);
      transform: translate3d(7px, 0, 0);
    }
  }

  @keyframes shake {
    from, to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    10%, 30%, 50%, 70%, 90% {
      -webkit-transform: translate3d(-7px, 0, 0);
      transform: translate3d(-7px, 0, 0);
    }

    20%, 40%, 60%, 80% {
      -webkit-transform: translate3d(7px, 0, 0);
      transform: translate3d(7px, 0, 0);
    }
  }

  .shake {
    -webkit-animation-name: shake;
    animation-name: shake;
  }
  /*--------页面头部---------*/
  .logIn_head_box {
    width: 100%;
    height: 70px;
    border-bottom: 2px solid #FFA000;
  }
  
  .logIn_head {
    margin: 0 auto;
    width: 1024px;
    height: 70px;
    line-height: 70px;
    border-bottom: 2px solid #FFA000;
  }

  .logIn_head .logo_img {
    margin-top: 6.5px;
  }

  .logIn_head .logIn_word {
    float: right;
    font-size: 16px;
    color: #555964;
  }

  .special {
    color: #FFA000;
    margin-left: 5px;
    padding-left: 5px;
    border-left: 1px solid #555964;
  }
  /*--------页面头部---------*/
  .logIn_box {
    width: 100%;
    height: 698px;
    background-color: #F2F2F2;
    padding-top: 50px;
  }

  .logIn_container {
    margin: 0 auto;
    background-color: #fff;
    width: 450px;
    height: 546px;
  }

  .logIn_container h3 {
    width: 450px;
    font-size: 18px;
    color: #555964;
    font-weight: normal;
    padding-top: 36px;
    margin-bottom: 27px;
    text-align: center;
  }

  .phone_box {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 390px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #E0E0E0;
    position: relative;
  }

  .yzm_box {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 390px;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #E0E0E0;
    position: relative;
  }

  .yzm_box .hasYzm {
    /*width: 60px;*/
    height: 36px;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    font-size: 14px;
    background-color: #4990E2;
    position: absolute;
    top: 6px;
    right: 0px;
    cursor: pointer;
    padding-left: 5px;
    padding-right: 5px;
  }

  .yzm_box .yzm_alert {
    color:#D0011B;
    font-size: 14px;
    position: absolute;
    bottom: -39px;
    left: 0;    
  }

  .phone_box .mb {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .phone_box .phone_alert {
    color:#D0011B;
    font-size: 14px;
    position: absolute;
    bottom: -39px;
    left: 0;
  }

  .mm {
    font-size: 16px;
    color: #555964;
    margin-right: 58px;
  }

  .yzm {
    font-size: 16px;
    color: #555964;
    margin-right: 42px;
  }

  .setMm {
    font-size: 16px;
    color: #555964;
    margin-right: 26px;
  }

  .confirmMm {
    font-size: 16px;
    color: #555964;
    margin-right: 26px;
  }

  .choose_box {
    margin: 0 auto; 
    margin-top: 39px;
    width: 390px;   
  }

  .choose_box .autoLog {
    margin-left: 20px;
    font-size: 14px;
    color: #83868E;
  }

  .choose_box .forgetPassword {
    margin-left: 216px;
    font-size: 14px;
    color: #83868E;
  }

  .btn_box {
    margin: 0 auto;
    margin-top: 17px;
    width: 390px;
    height: 50px;
  }

  .btn_box .btn_log {
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    background-color: #FFA000;
    cursor: pointer;
  }

  .btn_box .btn_log:hover {
    transition: all 0.5s ease;
    background-color: #FF7A00;
  }

  .thirdParty {
    margin-left: 30px;
    margin-top: 17px;
    font-size: 14px;
    color: #83868E;
  }
</style>



