<template>
  <div class="waitPay">
    <div class="allpay_box">
      <div class="allpay_th">
        <div class="left merchandise">商品</div>
        <div class="left afterSale">售后</div>
        <div class="left total">合计</div>
        <div class="left state">状态</div>
        <div class="left operate">操作</div>
      </div>
      <div class="wait_box">
        <div class="wait_form" v-for="order in items" :key="order">
          <div class="first_box">
            <span class="time">{{order.createdDate}}</span>
            <span class="form_num">订单号：{{order._id}}</span>
          </div>
          <ul class="second_box">
            <li class="left cargo_img_box">
              <img class="cargo_img" src="../../images/cargo1.png" alt="image">
            </li>
            <li class="left cargo_des_box">
              <p class="cargo_des1">{{order.caption}}</p>
              <p class="cargo_des2">有效期：{{order.endDate | formatDate}}</p>
              <p class="cargo_des3">地区：{{order.region}}</p>
            </li>
            <li class="left refund_box">退款</li>
            <li class="left price_box">¥{{order.totalprice}}</li>
            <li class="left state_box">{{order.state | frisco}}</li>     
            <li class="left operate_box">
              <div class="paypay" @click="pay(order)">付款</div>
              <div class="cancel_form" @click="cancel(order)">取消订单</div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'waitPay',
    data () {
      return {
        add: this.GLOBAL.address,
        items: [],
      }
    },
    components: {
    },
    mounted: function() {
      var that = this;
      var token = window.sessionStorage.getItem('token');
      axios.get(that.add + '/api/v1.0/order/list',{
        params: {
          token: token
        }
      })
      .then(function (res) {
        if (res.data.errcode == 200) {
          var a = res.data.data;
          var b = a.filter(function(ele,index,arr) {
            // console.log(ele);
            return ele.state == false; 
          });
          that.items = b;
          //console.log(that.items);
        } else {
          console.log('获取失败');
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      //取消订单
      cancel: function(order) {
        var that = this;
        that.$confirm('此操作将取消订单，是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios.get(that.add + '/api/v1.0/order/cancel',{
            params: {
              objectId: order._id,
            }
          }).then(function (res) {
            if (res.data.errcode == 200) {
              console.log(res);
              that.$message({
                type: 'success',
                message: '取消订单成功!'
              });
              //console.log(that.items);
            } else {
              console.log('获取失败');
            }
          })
          .catch(function (error) {
            console.log(error);
          });
        }).catch(() => {
          that.$message({
            type: 'info',
            message: '操作已取消'
          });          
        });
      },
      //付款
      pay: function(order) {
        var that = this;
        that.$router.push({ name: 'orderSuccess', params: { orderId: order._id, addressId: order.addressId, allMoney: order.totalprice }});
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .left {
    float: left;
  }
  
  .allpay_box {
    position: relative;
  }
  /*   allpay start   */
  .allpay_th {
    width: 766px;
    height: 34px;
    margin-top: 14px;
    color: #555964;
    font-size: 14px;
    background-color: #F4F4F4;
  }

  .allpay_th .merchandise {
    width: 377px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .afterSale {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .total {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .state {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }

  .allpay_th .operate {
    width: 97px;
    height: 34px;
    line-height: 34px;
    text-align: center;
  }
  /*   allpay end   */
  /*    wait_box start   */
  .wait_form {
    width: 766px;
    height: 160px;
    margin-top: 10px;
    background-color: #fff;
    border: 1px solid #FFF5E5;
  }

  .wait_form .first_box {
    width: 766px;
    height: 40px;
    line-height: 40px;
    color: #555964;
    font-size: 14px;
    background-color: #FFF5E5;
  }

  .first_box .time {
    font-weight: bold;
    margin-left: 20px;
  }

  .first_box .form_num {
    margin-left: 20px;
  }

  .second_box {
    width: 766px;
    height: 120px;
    line-height: 120px;
  }

  .cargo_img_box {
    margin-right: 30px;
    height: 120px;
  }

  .cargo_img {
    width: 80px;
    height: 80px;
    margin-top: 20px;
    margin-left: 20px;
  }

  .cargo_des_box {
    position: relative;
    width: 243px;
    height: 66px;
    margin-top: 22px;
    line-height: 25px;
  }

  .cargo_des_box .cargo_des1 {
    color: #555964;
    font-size: 16px;
  }

  .cargo_des_box .cargo_des2 {
    color: #83868E;
    font-size: 14px;
  }

  .cargo_des_box .cargo_des3 {
    color: #83868E;
    font-size: 14px;
  }

  .refund_box {
    width: 100px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .price_box {
    width: 100px;
    height: 120px;
    color: #555964;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .state_box {
    width: 96px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    border-right: 1px solid #FFF5E5;
  }

  .operate_box {
    width: 90px;
    height: 120px;
    color: #555964;
    font-size: 14px;
    text-align: center;
    position: relative;
  }

  .paypay {
    position: absolute;
    top: 37px;
    left: 20px;
    width: 50px;
    height: 28px;
    line-height: 28px;
    color: #fff;
    background-color: #FFA000;
    font-size: 14px;
    border-radius: 3px;
  }

  .paypay:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  .cancel_form {
    color: #83868E;
    font-size: 12px;
    line-height: 18px;
    position: absolute;
    top: 70px;
    left: 0px;
    right: 0px;
    margin: auto;
  }
  .cancel_form:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  /*    wait_box end   */
</style>



