<template>
  <div class="setHead">
    <div class="setHead_box">
      <form @submit.prevent="submit">
        <div class="nowHead">
          <span class="nowHead_word">当前头像：</span>
          <div class="head_img">
            <img style="width:80px; height:80px" :src=head alt="img" >
          </div>
          <vue-core-image-upload class="editHead_word" crop-ratio="1:1" text="编辑头像" v-bind:crop="true" extensions="png,gif,jpeg,jpg" inputOfFile="image" v-on:imageuploaded="imageuploaded" url="http://192.168.3.20:3300/api/v1.0/user/uploadimage" :data="abc">
          </vue-core-image-upload>
        </div>
      </form>
      <div class="nowAccount">
        <span class="nowAccount_word">账户：</span>{{username}}
      </div>
      <div class="nickname_box">
        <span class="nickname_word">昵称：</span><input type="text" class="nickname" id="nickname" :placeholder='nickname' v-model="nickname">
      </div>
      <div class="radio_box">
        <span class="gender_word">性别：</span>
        <input type="radio" id="secret" value="2" v-model="picked">
        <label for="secret">保密</label>
        <input type="radio" id="male" value="1" v-model="picked">
        <label for="male">男</label>
        <input type="radio" id="female" value="0" v-model="picked">
        <label for="female">女</label>
        <!-- <span>Picked: {{ picked }}</span> -->
      </div>
      <div class="saveBtn" @click="saveInfo(nickname, picked)">保存</div>
    </div>
  </div>
</template>

<script>
  import VueCoreImageUpload  from 'vue-core-image-upload';
  import Vue from 'vue';
  import axios from 'axios'
  export default {
    name: 'setHead',
    data () {
      return {
        add: this.GLOBAL.address,
        username: '',
        picked: '',
        nickname: '',
        show: true,
        abc: {
          token: ''
        },
      }
    },
    components: {
     'vue-core-image-upload': VueCoreImageUpload
   },
   mounted: function() {
    var that = this;
    var token = window.sessionStorage.getItem('token');
    that.token = token;
    that.abc.token = token;
    axios.get(that.add + '/api/v1.0/user/userinfo',{
      params: {
        token: token
      }
    })
    .then(function (res) {
      // console.log(res.data.data);
      // console.log(that.token);
      // that.src = res.data.data.head;
      that.username = res.data.data.username;
      that.nickname = res.data.data.nickname;
      that.picked = res.data.data.sex;
      that.head = res.data.data.head;
    })
    .catch(function (error) {
      console.log(error);
    });
  },
  methods: {
      //上传头像
      imageuploaded(res) {
        //console.log(res);
        var that = this;
        if (res.errcode == 200) {
          that.head = res.data;
          that.$emit('listenToChildEvent', that.head);
          that.$message({
            type: 'success',
            message: '上传头像成功!'
          });
        } else {
          that.$message.error('上传头像失败！');
        }
      },
      //保存信息
      saveInfo(nickname, picked) {
        var that = this;
        var token = window.sessionStorage.getItem('token');
        axios({
          method: 'post',
          url: that.add + '/api/v1.0/user/update',
          data: `nickname=${this.nickname}&sex=${this.picked}&head=${this.src}&token=${token}`
        }).then(function(res){
          if(res.data.errcode == 404) {
              that.$message.error('保存个人信息失败！');
          } else {
              that.$message({
              type: 'success',
              message: '保存个人信息成功!'
            });
          }
        },function(res){
          console.log('注册失败，未通过服务端校验'+ res.status);
        });  
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  input {
    border: none;
    outline:medium;
  }

  #nickname {
    width: 134px;
    height: 28px;
    border: 1px solid #E0E0E0;
  }

  .setHead_box {
    width: 767px;
    height: 431px;
    font-size: 14px;
    color: #555964;
  }

  .nowHead {
    width: 162PX;
    height:  80px;
    padding-top: 41.5px;
    margin-left: 20px;
    position: relative;
  }

  .nowHead .nowHead_word {
    position: absolute;
    top: 39px;
    left: 0;
  }

  .nowHead .editHead_word {
    width: 80px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    color: #fff;
    background-color: rgba(0,0,0,0.5);
    position: absolute;
    text-decoration: underline;
    bottom: 2px;
    right: 2px;
    cursor: pointer;
  }

  .nowHead .head_img {
    border: 2px solid #D8D8D8;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 80px;
    height: 80px;
  }

  .nowAccount {
    margin-left: 20px;
    margin-top: 20px;
  }

  .nowAccount .nowAccount_word {
    margin-right: 35px;
  }

  .nickname_box {
    margin-left: 20px;
    margin-top: 22px;
  }

  .nickname_box .nickname_word {
    margin-right: 35px;
  }

  .radio_box {
    margin-left: 20px;
    margin-top: 22px;
  }

  .radio_box .gender_word {
    margin-right: 35px;
  }

  label{
    margin-right: 20px;
  }

  .saveBtn {
    width: 80px;
    height: 28px;
    line-height: 29px;
    color: #fff;
    text-align: center;
    background-color: #FFA000;
    border-radius: 3px;
    margin-top: 30px;
    margin-left: 100px;
  }

  .saveBtn:hover {
    cursor: pointer;
    background-color: #FF7A00;
    transition: all 0.5s ease;
  }
</style>


